      //       }
      //     }
      //   }
      //
      //   pdf.save(companyName+' '+self.name+'總評.pdf');
      //
      //
      // });

      html2canvas(document.body, {
        allowTaint: true,
        scale: 2 // 提升画面质量，但是会增加文件大小
      }).then(function (canvas) {

        // 得到canvas画布的单位是px 像素单位
        var contentWidth = canvas.width
        var contentHeight = canvas.height

        console.log('contentWidth', contentWidth)
        console.log('contentHeight', contentHeight)
        // 将canvas转为base64图片
        var pageData = canvas.toDataURL('image/jpeg', 1.0)

        // 设置pdf的尺寸，pdf要使用pt单位 已知 1pt/1px = 0.75   pt = (px/scale)* 0.75
        // 2为上面的scale 缩放了2倍
        var pdfX = (contentWidth + 10) / 2 * 0.75
        var pdfY = (contentHeight) / 2 * 0.75 // 500为底部留白

        // 设置内容图片的尺寸，img是pt单位
        var imgX = pdfX;
        var imgY = (contentHeight / 2 * 0.75); //内容图片这里不需要留白的距离

        // 初始化jspdf 第一个参数方向：默认''时为纵向，第二个参数设置pdf内容图片使用的长度单位为pt，第三个参数为PDF的大小，单位是pt
        var PDF = new jsPDF('', 'pt', [pdfX, pdfY])

        // 将内容图片添加到pdf中，因为内容宽高和pdf宽高一样，就只需要一页，位置就是 0,0
        PDF.addImage(pageData, 'jpeg', 0, 0, imgX, imgY)
        PDF.save(companyName + ' ' + self.name + '總評.pdf');
      })
    },
    getFormList(answer_id, answer_eid) {
      let url = "";

      if(!!answer_eid){
        url = "/rise_api/public/index.php/view/getanswer_1_eid/" + answer_eid;
      }else if(!!answer_id){
        url = "/rise_api/public/index.php/view/getanswer_1_id/" + answer_id;
      }else{
        console.log('no answer id or answer eid!');
        return false;
      }

      this.axios
        // .get("/rise_api/public/index.php/view/getanswer_1_id/" + this.$route.params.answerId)
