          document.title = response_data.profile.name;
          this.appreciation_checkbox = response_data.question.appreciation_checkbox == 1 ? true : false;
          if (this.appreciation_checkbox) {
            this.finalScoreContent_checkbox = !this.appreciation_checkbox;
          } else {
            this.finalScoreContent_checkbox = response_data.question.finalScoreContent_checkbox == 1 ? true : false
          }
