              icon: "success",
              title: "回答結束",
              showConfirmButton: false,
              timer: 500
            }).then(() => {
              this.$router.replace({
                name: "view_home"
              });
            });

          }
          else if (this.appreciation_checkbox) {
            this.finalScoreContent_checkbox = !this.appreciation_checkbox;
            console.log('push appreciationContent');
            this.$router.replace({
              name: "appreciationContent",
              params: {
                form_id: this.$route.params.form_id,
                username: localStorage.getItem("view_username"),
              },
            });
          }
          else if (this.finalScoreContent_checkbox) {
            //圖表數值
            // console.log(response_data)
            self.name = response_data.profile.name;

            let chart_json = self.chart_json;
            let chart_json2 = self.chart_json2;
            let chart_json3 = self.chart_json3;

            $.each(response_data.charts, function (key, value) {
              if (value.part === '1') {
                chart_json.push({
                  data: JSON.parse(value.chart_json),
                  type: value.type,
                  description: value.description,
                  title: value.title
                });
              } else if (value.part === '2') {
                chart_json2.push({
                  data: JSON.parse(value.chart_json),
                  type: value.type,
                  description: value.description,
                  title: value.title
                });
              } else if (value.part === '3') {
                chart_json3.push({
                  data: JSON.parse(value.chart_json),
                  type: value.type,
                  description: value.description,
                  title: value.title
                });
              }
            });

            let chart_form_0 = []; //暫存構面名稱(避免重複)

            $.each(chart_json, function (key, value) {
              let chart_form = [];
              $.each(value.data.dimensions, function (key2, value2) {
                self.chart_form.push({
                  historyArr: value2.historyArr,
                  historyArr2: value2.historyArr2,
                  subjectArr: value2.subjectArr[0],
                  myscoreArr: 0,
                  chartIndex: key
                });
                chart_form.push({
                  historyArr: value2.historyArr,
                  historyArr2: value2.historyArr2,
                  subjectArr: value2.subjectArr[0],
                  myscoreArr: 0,
                  chartIndex: key
                });
              });
              self.chart1.push({
                chart_json: value,
                chart_form: chart_form
              })
            });
            $.each(chart_json2, function (key, value) {
              let chart_form2 = [];
              $.each(value.data.dimensions, function (key2, value2) {
                self.chart_form2.push({
                  historyArr: value2.historyArr,
                  historyArr2: value2.historyArr2,
                  subjectArr: value2.subjectArr[0],
                  myscoreArr: 0,
                  chartIndex: key
                });
                chart_form2.push({
                  historyArr: value2.historyArr,
                  historyArr2: value2.historyArr2,
                  subjectArr: value2.subjectArr[0],
                  myscoreArr: 0,
                  chartIndex: key
                });
              });
              self.chart2.push({
                chart_json: value,
                chart_form: chart_form2
              })
            });
            $.each(chart_json3, function (key, value) {
              let chart_form3 = [];
              $.each(value.data.dimensions, function (key2, value2) {
                self.chart_form3.push({
                  historyArr: value2.historyArr,
                  historyArr2: value2.historyArr2,
                  subjectArr: value2.subjectArr[0],
                  myscoreArr: 0,
                  chartIndex: key
                });
                chart_form3.push({
                  historyArr: value2.historyArr,
                  historyArr2: value2.historyArr2,
                  subjectArr: value2.subjectArr[0],
                  myscoreArr: 0,
                  chartIndex: key
                });
              });
              self.chart3.push({
                chart_json: value,
                chart_form: chart_form3
              })
            });

            // //拿最下面的結尾語(有需要進一步了解.....)

            this.titleNoteContent_checkbox = response_data.question.titleNoteContent_checkbox == 1 ? true : false;
            this.final_content = response_data.question.final_content;
            this.title_content = response_data.question.title_content;
            this.titleNote_content = response_data.question.titleNote_content;

            this.company_checkbox = response_data.question.company_checkbox == 1 ? true : false;


            this.totalScoreTitle_checkbox = response_data.question.totalScoreTitle_checkbox == 1 ? true : false;
            this.totalScoreChart_checkbox = response_data.question.totalScoreChart_checkbox == 1 ? true : false;
            this.totalScoreTitle_content = response_data.question.totalScoreTitle_content;//數位轉型準度總分

            this.fullMarks_content = response_data.question.fullMarks_content;
            this.averageScore_content = response_data.question.averageScore_content;

            this.distribution_checkbox = response_data.question.distribution_checkbox == 1 ? true : false;
            this.distribution_content = response_data.question.distribution_content;

            this.capability_checkbox = response_data.question.capability_checkbox == 1 ? true : false;
            this.capability_content = response_data.question.capability_content;

            this.capabilityNote_checkbox = response_data.question.capabilityNote_checkbox == 1 ? true : false;
            this.capabilityNote_content = response_data.question.capabilityNote_content;


            this.analytical_checkbox = response_data.question.analytical_checkbox == 1 ? true : false;
            this.analytical_content = response_data.question.analytical_content;

            this.analyticalNote_checkbox = response_data.question.analyticalNote_checkbox == 1 ? true : false;
            this.analyticalNote_content = response_data.question.analyticalNote_content;

            this.sectionOne_checkbox = response_data.question.sectionOne_checkbox == 1 ? true : false;
            this.sectionOne_content = response_data.question.sectionOne_content;
            this.sectionTwo_checkbox = response_data.question.sectionTwo_checkbox == 1 ? true : false;
            this.sectionTwo_content = response_data.question.sectionTwo_content;
            this.technology_checkbox = response_data.question.technology_checkbox == 1 ? true : false;
            this.technology_content = response_data.question.technology_content;
            this.technologyNote_checkbox = response_data.question.technologyNote_checkbox == 1 ? true : false;
            this.technologyNote_content = response_data.question.technologyNote_content;
            this.sectionThree_checkbox = response_data.question.sectionThree_checkbox == 1 ? true : false;
            this.sectionThree_content = response_data.question.sectionThree_content;
            this.suggest_checkbox = response_data.question.suggest_checkbox == 1 ? true : false;
            this.suggest_content = response_data.question.suggest_content;
            this.image_checkbox = response_data.question.image_checkbox == 1 ? true : false;
            this.blue_checkbox = response_data.question.blue_checkbox == 1 ? true : false;
            this.image = response_data.question.image;
            this.banner = response_data.question.banner;
            this.quadrantChart_checkbox = response_data.question.quadrantChart_checkbox == 1 ? true : false;
            this.quadrantChart_x = response_data.question.quadrantChart_x;
            this.quadrantChart_y = response_data.question.quadrantChart_y;
            this.quadrantChart_x_avg = response_data.question.quadrantChart_x_avg;
            this.quadrantChart_y_avg = response_data.question.quadrantChart_y_avg;
            this.quadrantChart_one = response_data.question.quadrantChart_one;
            this.quadrantChart_two = response_data.question.quadrantChart_two;
            this.quadrantChart_three = response_data.question.quadrantChart_three;
            this.quadrantChart_four = response_data.question.quadrantChart_four;
            this.time = response_data.answer.time;
            if (response_data.answer.companyName !== null && response_data.answer.companyName !== undefined && response_data.answer.companyName !== '') {
              this.companyName = response_data.answer.companyName;
            } else {
              this.companyName = localStorage.cname !== null && localStorage.cname !== undefined ? localStorage.cname : "guest";//公司名稱
            }
            if (response_data.answer.companyNum !== null && response_data.answer.companyNum !== undefined && response_data.answer.companyNum !== '') {
              this.companyNum = response_data.answer.companyNum;
            } else {
              this.companyNum = localStorage.cnum !== null && localStorage.cnum !== undefined ? localStorage.cnum : "12345678";//公司統編
            }


            // this.maxScore = response_data.result.maxScore;

            let _this = this;



            // //填完的構面結果
            let score_json = JSON.parse(response_data.answer.score);
            $.each(score_json, function (key, value) {
              _this.score = self.floatAdd(_this.score, value.score);
            });
            let dimensions_name = [];
            dimensions_name.push(Object.keys(score_json));
            let data = [];
            // //填入構面
            $.each(dimensions_name, function (key, value) {
              data.push({
                dimensions: value,
              });
            });
            // //將分數存為array
            let score = Array();
            $.each(data[0].dimensions, function (key, value) {
              score.push(score_json[data[0].dimensions[key]].score);
              if (data[0].dimensions[key] === self.quadrantChart_y) {
                self.quadrantChart_y_score = score_json[data[0].dimensions[key]].score
              }
            });
            data[0]["score"] = score;

            this.test2(data[0], self.chart_form);
            this.test2(data[0], self.chart_form2);
            this.test2(data[0], self.chart_form3);

            self.chart1.forEach((t) => {
              this.test2(data[0], t.chart_form);

            });
            self.chart2.forEach((t) => {
              this.test2(data[0], t.chart_form);

            });
            self.chart3.forEach((t) => {
              this.test2(data[0], t.chart_form);

            });




            //總分畫圖
