              this.totalBar(totalCtx, 100, self.total_count, self.fullMarks_content, self.averageScore_content);
              //this.totalBar(totalCtx, self.maxScore, self.total_count,self.fullMarks_content,self.averageScore_content);
            })



            // //畫圖
            for (let i = 0; i < chart_json.length; i++) {
              //等待DOM生成完畢
              self.$nextTick(function () {
                var ctx = document.getElementById("chart_" + i).getContext("2d");
                // 如果要看到其他圖形 要去設定後台設定圖形的構面(構面要有分數才會有圖形)
                // console.log(chart_json[i].data);
                // console.log(self.chart_form);
                if (chart_json[i].type == "bar") {
                  this.bar(ctx, chart_json[i].data, self.chart_form);
                } else if (chart_json[i].type == "pie") {
                  // (圓環圖沒有自己的分數時會隱藏)
                  this.pie(ctx, chart_json[i].data, self.chart_form);
                } else if (chart_json[i].type == "line") {
                  this.line(ctx, chart_json[i].data, self.chart_form);
                } else if (chart_json[i].type == "horizontalBar") {
                  this.horizontalBar(ctx, chart_json[i].data, self.chart_form);
                } else if (chart_json[i].type == "radar") {
                  this.radar(ctx, chart_json[i].data, self.chart_form);
