                // console.log(chart_json[i].data);
                // console.log(self.chart_form);
                if (chart_json2[i].type == "bar") {
                  this.bar(ctx, chart_json2[i].data, self.chart_form2);
                } else if (chart_json2[i].type == "pie") {
