          intersect: false,
        },
        hover: {
          mode: "nearest",
          intersect: true,
        },
        title: {
          display: true,
          text: "",
        },
        scales: {
          yAxes: [
            {
              ticks: {
                beginAtZero: true,
                // fontSize: 10,
                min: 0,
                max: parseFloat(maxscore),
                stepSize: parseFloat(scoreRange)
              },
              scaleLabel: {
                display: true,
                labelString: "分數",
              },
            },
          ],
          xAxes: [
            {
              ticks: {
                // fontSize: 12,
              },
            },
          ],
        },
      };
      this.html_chart = new Chart(ctx, {
        type: "line",
        data: marksData,
        options: chartOptions,
        responsive: true,
        maintainAspectRatio: false,
      });
    },
    //id ,分數資料(歷史分數 構面名稱 分數上限 分數間距)  圖表設定(歷史分數 實際得分 構面名稱)
    radar(ctx, data, data2) {
      let scoreArr = [];
      let subjectArr = [];
      let historyArr = [];
      let historyArr2 = [];
      let chartTitle = data.chartTitle;
      let maxscore = data.maxscore;
      let scoreRange = data.scoreRange;
      $.each(data.dimensions, function (key, value) {
        subjectArr.push(value.subjectArr);
        historyArr.push(value.historyArr);
        historyArr2.push(value.historyArr2);
      });
      //自己的分數 (進行構面比對 一樣的為該構面的得分)
      $.each(data2, function (key, value) {
        $.each(subjectArr, function (key2, value2) {
          if (value.subjectArr == value2[0]) {
            scoreArr.push(value.myscoreArr);
          }
        });
      });
      let color1 = "#ffb81c"

      if (this.blue_checkbox) {
        color1 = "#d8973d"
      }

      var marksData = {
        labels: subjectArr,
        datasets: [
          {
            label: "我的分數",
            borderColor: color1,
            data: scoreArr,
            fill: false,
          },
        ],
      };
      if (historyArr.length > 0) {
        let dataset = {
          label: "產業平均",
          borderColor: "#00816d",
          data: historyArr
        };
        marksData.datasets.push(dataset);
      }
      if (historyArr2.length > 0) {
        if (historyArr2[0] !== "0" && historyArr2[0] !== "") {
          let dataset = {
            label: "同業平均",
            borderColor: "#7f7f7f",
            data: historyArr2
          };
          marksData.datasets.push(dataset);
        }
      }
      var chartOptions = {
        legend: {
          display: true,
          position: 'bottom',
          maxHeight: 10,
          labels: {
            boxWidth: 10,
            font: {
              size: 2
            },
            padding: 2
          }
        },
        responsive: true,
        maintainAspectRatio: false,
        tooltips: {
          mode: "index",
          intersect: false
        },
        hover: {
          mode: "nearest",
          intersect: true
        },
        title: {
          display: true,
          text: ""
        },
        scales: {
          yAxes: [
            {
              ticks: {
                beginAtZero: true,
                // fontSize: 10,
                min: 0,
                max: parseFloat(maxscore),
                stepSize: parseFloat(scoreRange)
              },
              scaleLabel: {
                display: true,
                labelString: "分數"
              }
            }
          ],
          xAxes: [
            {
              ticks: {
                // fontSize: 12
              }
            }
          ]
        }
      };

      this.html_chart = new Chart(ctx, {
        type: "radar",
        data: marksData,
        options: chartOptions,
        responsive: true,
        maintainAspectRatio: false
      });
    },
  },
};
</script>

<style lang="scss">
@import "../../assets/scss/Digital";
</style>


<style>
.form-wrapper {
  padding: 0;
  width: 75%;
}

.form-question {
  padding: 20px;
  border-bottom: 2px dashed #bbb;
}

.send {
  margin: 10px 0;
  text-align: center;
}

.error .q-title {
  color: #f05050;
}

#read-first {
  padding: 0 15%;
}

.font_color {
  color: #ffffff;
  background: #4b506a;
  padding-bottom: 7px;
}

.font_color2 {
  color: #ffffff;
  background: #ffb54a;
  padding-bottom: 4px;
  font-size: 25px;
}

font {
  font-family: Microsoft JhengHei;
}

.strong_font {
  color: #1e7fc8;
  margin-left: -4px;
}
</style>



