<!--
	前台填寫問卷
-->
<template>
  <div
    v-show="loadDone === true"
    id="answer-layout"
    class="container-lg report-container"
  >
    <answer-banner
      id="answer-banner"
      :banner="banner"
    />
    <answer-title
      id="answer-title"
      :time="time"
      :company-num="companyNum"
      :company-name="companyName"
      :company-checkbox="companyCheckbox"
      :title-note-content="titleNoteContent"
      :title-note-content-checkbox="titleNoteContentCheckbox"
      :title-content="titleContent"
      :pdf-generator="getPdf"
      :career-name="careerName"
    />
    <answer-summary
      id="answer-summary"
      :section-one-content="sectionOneContent"
      :total-score-title-content="totalScoreTitleContent"
      :total-count="totalCount"
      :max-score="maxScore"
      :summary-option="summaryOption"
      :distribution-content="distributionContent"
      :capability-content="capabilityContent"
      :chart-options="chart1"
      :wording="wording"
      :industry-option="industryOption"
      :industry-pr-option="industryPrOption"
      :career-name="careerName"
      :career-option="careerOption"
      :career-pr-option="careerPrOption"
      :chart-max-score="chartMaxScore"
      :industry-pr="industryPr"
      :career-pr="careerPr"
      :industry-chart-wordings="industryChartWordings"
      :career-chart-wordings="careerChartWordings"
      :level-content="levelContent"
      :industry-pr-wording="industryPrWording"
      :career-pr-wording="careerPrWording"
      :dimension-introductions="dimensionIntroductions"
    />

    <answer-analysis
      id="answer-analysis"
      :sub-dimension-scores="subDimensionScores"
      :dimension-descriptions="dimensionDescriptions"
      :pr-range="prRange"
      :career-name="careerName"
    />

    <answer-reference
      id="answer-reference"
      :references="references"
    />
    <answer-conclusion
      id="answer-conclusion"
      :conclusions="conclusions"
      :contacts="contacts"
      :links="links"
    />
  </div>
</template>

<script>
import _ from 'lodash';

import AnswerBanner from '../../components/AnswerBanner2/index.vue';
import AnswerTitle from '../../components/AnswerTitle2/index.vue';
import AnswerSummary from '../../components/AnswerSummary2/index.vue';
import AnswerRecommend from '../../components/AnswerRecommend2/index.vue';
import AnswerAnalysis from '../../components/AnswerAnalysis2/index.vue';
import AnswerReference from '../../components/AnswerReference2/index.vue';
import AnswerConclusion from '../../components/AnswerConclusion2/index.vue';

import {
  buildCareerChartOption,
  buildCareerPrOption,
  buildIndustryChartOption,
  buildIndustryPrOption,
  buildSummaryChartOption,
  buildUrl,
  buildUrlById,
  buildUrlByEid,
  calPercentage,
  floatAdd,
  generatePdf,
  generatePdf2,
  generatePdf3,
  generatePdf4,
  generatePdf5,
  generateJpg,
  initChartWording,
  isChecked,
  parseCareer,
  parseIndustryScores,
  parseWording,
  sumCareerScore,
} from './viewHelper';

import {
  CONCLUSIONS,
  CONTACTS,
  DIMENSION_INTRODUCTIONS,
  DIMENSION_DESCRIPTIONS,
  DIMENSION_RELATIONS,
  LINKS,
  MAIN_DIMENSIONS,
  MAX_PR,
  PR_RANGE,
  REFERENCES,
  REFERENCES_NEW,
} from './config';

export default {
  components: {
    AnswerBanner,
    AnswerTitle,
    AnswerSummary,
    AnswerRecommend,
    AnswerAnalysis,
    AnswerReference,
    AnswerConclusion,
  },
  data() {
    return {
      dimensions: [],
      dimensionIntroductions: DIMENSION_INTRODUCTIONS,
      dimensionDescriptions: DIMENSION_DESCRIPTIONS,
      finalContent: '', //結尾文字
      titleContent: '', //總評標題文字
      titleNoteContent: '', //總評標題備註
      companyCheckbox: true, //決定公司簡介欄是否顯示
      titleNoteContentCheckbox: true, //決定總評標題備註是否顯示
      appreciationCheckbox: false,
      finalScoreContentCheckbox: false,
      totalScoreTitleCheckbox: true,
      totalScoreChartCheckbox: true,
      totalScoreTitleContent: '',
      fullMarksContent: '',
      careerMarksContent: '',
      averageScoreContent: '',
      distributionCheckbox: true,
      distributionContent: '',
      capabilityCheckbox: true,
      capabilityContent: '',
      capabilityNoteCheckbox: true,
      capabilityNoteContent: '',
      analyticalCheckbox: true,
      analyticalContent: '',
      analyticalNoteCheckbox: true,
      analyticalNoteContent: '',
      sectionOneCheckbox: true,
      sectionOneContent: '',
      sectionTwoCheckbox: true,
      sectionTwoContent: '',
      technologyCheckbox: true,
      technologyContent: '',
      technologyNoteCheckbox: true,
      technologyNoteContent: '',
      sectionThreeCheckbox: true,
      sectionThreeContent: '',
      suggestCheckbox: true,
      suggestContent: '',
      imageCheckbox: true,
      blueCheckbox: false,
      image: '',
      time: '',
      banner: '',
      name: '',
      score: 0,
      maxScore: 0,
      htmlChart: '',
      totalCount: 0, //總分
      totalEvaluate: '', //總評
      maxEvaluate: '',
      evaluateDes: '',
      dimensionNames: [],
      industryChartWordings: [],
      careerChartWordings: [],
      chartForm1: [],
      chartForm2: [],
      chartForm3: [],
      chartJson1: [],
      chartJson2: [],
      chartJson3: [],
      chart1: [],
      chart2: [],
      chart3: [],
      companyNum: '',
      companyName: '',
      loadDone: false,
      quadrantImg: '',
      quadrant: '',
      wording: {},
      industryScores: [],
      careerSum: 0,
      careerName: '同業',
      summaryOption: {},
      industryOption: {},
      industryPrOption: {},
      industryPrWording: [],
      careerOption: {},
      careerPrOption: {},
      careerPrWording: [],
      chartMaxScore: 0,
      exampleCases: '',
      industryPrScores: {},
      scoreJson: {},
      industryPr: 0,
      industryPrs: [],
      careerPr: 0,
      careerPrs: [],
      quadrantChartCheckbox: true,
      quadrantChartX: '',
      quadrantChartY: '',
      quadrantChartXScore: 0,
      quadrantChartYScore: 0,
      quadrantChartXAvg: '',
      quadrantChartYAvg: '',
      quadrantChartOne: '',
      quadrantChartTwo: '',
      quadrantChartThree: '',
      quadrantChartFour: '',
      quadrantChartResult: '',
      subDimensionScores: [],
      references: REFERENCES_NEW,
      conclusions: CONCLUSIONS,
      links: LINKS,
      contacts: CONTACTS,
      prRange: PR_RANGE,
      levelContent: {},
    };
  },
  created() {
    this.getFormList();
  },

  methods: {
    showAnswerResponseData(responseData) {
      const { question, answer, charts, profile, properties } = responseData;

      this.setTitleAndName(profile.name);
      this.showAnswerReport(question, answer, charts, profile, properties);
      this.loadDone = true;
    },
    getFormList() {
      const answerEid = this.$route.params.answerEid;
      const answerId = this.$route.params.answerId;
      const formId = this.$route.params.form_id;
      const username = this.$route.params.username;
      const self = this;

      if (!!answerEid) {
        console.log('viewByEid mode.');

        this.axios.get(buildUrlByEid(answerEid)).then((response) => {
          const responseData = response.data;
          self.showAnswerResponseData(responseData);
          // console.log("answer response: ", responseData);

          /*
          const { question, answer, charts, profile, properties } =
            responseData;

          self.setTitleAndName(profile.name);
          self.showAnswerReport(question, answer, charts, profile, properties);
          self.loadDone = true;
          */
        });
      } else if (!!answerId) {
        console.log('viewById mode.');

        this.axios.get(buildUrlById(answerId)).then((response) => {
          const responseData = response.data;
          // console.log("answer response: ", responseData);
          self.showAnswerResponseData(responseData);

          /*
          const { question, answer, charts, profile, properties } =
            responseData;

          self.setTitleAndName(profile.name);
          self.showAnswerReport(question, answer, charts, profile, properties);
          self.loadDone = true;
          */
        });
      } else if (!!formId && !!username) {
        console.log('viewFromForm mode.');

        this.axios.get(buildUrl(formId, username)).then((response) => {
          // console.log(formId + '/' + username);

          const responseData = response.data;
          // console.log('answer response: ', responseData);
          self.showAnswerResponseData(responseData);

          /*
          const { question, answer, charts, profile, properties } =
            responseData;

          self.setTitleAndName(profile.name);
          self.showAnswerReport(question, answer, charts, profile, properties);
          self.loadDone = true;
          */
        });
      }
    },

    getPdf(companyName) {
      // generatePdf(companyName, this.name);
      // generatePdf2(companyName, this.name);
      generatePdf3(companyName, this.name); // curr prod
      // generateJpg(companyName, this.name);
      // generatePdf4(companyName, this.name);
      // generatePdf5(companyName, this.name);
    },

    setTitleAndName(title) {
      document.title = title;
      this.name = title;
    },

    showAnswerReport(question, answer, charts, profile, properties) {
      this.initProperties(properties);
      this.initCareer(answer);

      this.initTitleContent(answer);
      this.initRecommandContent(question);

      this.initChartSetting(question);
      this.initChartContent(answer, question);
      this.initLevelContent(answer, question);

      this.initSubDimensionScores();
    },

    initProperties(properties) {
      // 圖表數值
      this.wording = parseWording(properties);
      // console.log("wording: ", this.wording);

      this.industryScores = parseIndustryScores(properties);
      // console.log("industry scores: ", this.industryScores);

      this.exampleCases = properties.exampleCases;

      this.industryPrScores = properties.prScores;
    },

    initCareer(answer) {
      this.careerName = parseCareer(answer);
      // console.log("career name: ", this.careerName);

      this.careerSum = sumCareerScore(this.industryScores, this.careerName);
      // console.log("career sum: ", this.careerSum);
    },

    initChartSetting(question) {
      this.dimensionNames = MAIN_DIMENSIONS; // question.final_score.dimensions.filter(dimension => !!dimension);
      console.log('dimension names: ', this.dimensionNames);

      this.industryChartWordings = initChartWording(
        this.industryScores,
        this.wording.industry,
        this.dimensionNames
      );
      console.log('industry chart wording: ', this.industryChartWordings);

      this.careerChartWordings = initChartWording(
        this.industryScores,
        this.careerName,
        this.dimensionNames
      );
      console.log('career chart wording: ', this.careerChartWordings);
    },

    initRecommandContent(question) {
      this.titleNoteContentCheckbox = isChecked(
        question.titleNoteContent_checkbox
      );
      this.finalContent = question.final_content;
      this.titleContent = question.title_content;
      this.titleNoteContent = question.titleNote_content;

      this.companyCheckbox = isChecked(question.company_checkbox);

      this.totalScoreTitleCheckbox = isChecked(
        question.totalScoreTitle_checkbox
      );
      this.totalScoreChartCheckbox = isChecked(
        question.totalScoreChart_checkbox
      );
      this.totalScoreTitleContent = question.totalScoreTitle_content; //數位轉型準度總分

      this.fullMarksContent = question.fullMarks_content;
      this.averageScoreContent = question.averageScore_content;

      this.distributionCheckbox = isChecked(question.distribution_checkbox);
      this.distributionContent = question.distribution_content;

      this.capabilityCheckbox = isChecked(question.capability_checkbox);
      this.capabilityContent = question.capability_content;

      this.capabilityNoteCheckbox = isChecked(question.capabilityNote_checkbox);
      this.capabilityNoteContent = question.capabilityNote_content;

      this.analyticalCheckbox = isChecked(question.analytical_checkbox);
      this.analyticalContent = question.analytical_content;

      this.analyticalNoteCheckbox = isChecked(question.analyticalNote_checkbox);
      this.analyticalNoteContent = question.analyticalNote_content;

      this.sectionOneCheckbox = isChecked(question.sectionOne_checkbox);
      this.sectionOneContent = question.sectionOne_content;

      this.sectionTwoCheckbox = isChecked(question.sectionTwo_checkbox);
      this.sectionTwoContent = question.sectionTwo_content;

      this.technologyCheckbox = isChecked(question.technology_checkbox);
      this.technologyContent = question.technology_content;

      this.technologyNoteCheckbox = isChecked(question.technologyNote_checkbox);
      this.technologyNoteContent = question.technologyNote_content;

      this.sectionThreeCheckbox = isChecked(question.sectionThree_checkbox);
      this.sectionThreeContent = question.sectionThree_content;

      this.suggestCheckbox = isChecked(question.suggest_checkbox);
      this.suggestContent = question.suggest_content;

      this.imageCheckbox = isChecked(question.image_checkbox);
      this.image = question.image;

      this.blueCheckbox = isChecked(question.blue_checkbox);

      this.banner = question.banner;
      this.maxScore = question.maxScore;
    },

    initTitleContent(answer) {
      this.time = answer.time;

      if (
        answer.companyName !== null &&
        answer.companyName !== undefined &&
        answer.companyName !== ''
      ) {
        this.companyName = answer.companyName;
      } else {
        this.companyName =
          localStorage.cname !== null && localStorage.cname !== undefined
            ? localStorage.cname
            : 'guest'; //公司名稱
      }

      if (!!answer.companyNum) {
        this.companyNum = answer.companyNum;
      } else {
        this.companyNum =
          localStorage.cnum !== null && localStorage.cnum !== undefined
            ? localStorage.cnum
            : '12345678'; //公司統編
      }
    },
    initLevelContent(answer, question) {
      const finalScore = question.final_score;
      const levels = finalScore.level;
      const maxLevel = levels.length;
      const myScore = this.totalCount;

      let myLabel = '';
      let myDesc = '';
      _.forEach(levels, (level) => {
        const { min, max, des, des2 } = level;
        let sMin = parseFloat(min);
        let sMax = parseFloat(max);

        if (sMin <= myScore && myScore <= sMax) {
          myLabel = des;
          myDesc = des2;
        }
      });

      this.levelContent = {
        myLabel,
        maxLevel,
        myDesc,
      };
    },

    getCareerScore() {
      if (!!this.careerName && !!this.industryScores) {
        const careerScores = this.industryScores.find(
          (career) => career.name === this.careerName
        );

        if (!!careerScores) {
          const totalScore = careerScores.dimensions.find(
            (dimension) => dimension.name === '競爭力總分'
          );

          if (!!totalScore) {
            return totalScore.score;
          }
        }
      }

      return '';
    },

    processScore(answer, question) {
      const self = this;
      let origScoreJson = JSON.parse(answer.score);

      _.forOwn(origScoreJson, function (value, key) {
        const { des, maxScore, score } = value;

        let newMaxScore = parseFloat(maxScore.toFixed(2));
        let newScore = parseFloat(score.toFixed(2));

        self.scoreJson[key] = {
          des: des,
          maxScore: newMaxScore,
          score: newScore,
        };
      });

      this.initMyScore();
      this.initTotalCount(question);
      this.initPrValue();
      this.initDimensionPrValues();
    },

    initMyScore() {
      const self = this;

      _.forEach(this.dimensionNames, (dimensionName) => {
        const myDimensionScore = self.scoreJson[dimensionName].score;
        const dimensionMaxScore = self.scoreJson[dimensionName].maxScore;
        const newMyScore = calPercentage(
          myDimensionScore,
          dimensionMaxScore,
          1
        );

        const industryDimensionChartWording = self.industryChartWordings.find(
          (chartWording) => chartWording.dimension === dimensionName
        );

        if (!!industryDimensionChartWording) {
          industryDimensionChartWording.maxScore = dimensionMaxScore;
          industryDimensionChartWording.myScore = newMyScore;
        }

        const careerDimensionChartWording = self.careerChartWordings.find(
          (chartWording) => chartWording.dimension === dimensionName
        );

        if (!!careerDimensionChartWording) {
          careerDimensionChartWording.maxScore = dimensionMaxScore;
          careerDimensionChartWording.myScore = newMyScore;
        }
      });
    },

    initTotalCount(question) {
      const self = this;
      const finalScore = question.final_score;

      //總分
      _.forEach(finalScore.dimensions, (dimension) => {
        if (!!self.scoreJson[dimension]) {
          self.totalCount = floatAdd(
            self.totalCount,
            self.scoreJson[dimension].score
          );
        }
      });
    },

    initSummaryChart() {
      //總分畫圖, done
      this.careerMarksContent = this.getCareerScore();

      this.$nextTick(function () {
        const categories = [
          `${this.careerName}業平均`,
          `${this.wording.industry}平均`,
          `您的分數`,
        ];

        this.summaryOption = buildSummaryChartOption(
          categories,
          this.maxScore, // 100,
          this.totalCount,
          this.fullMarksContent,
          this.careerMarksContent
        );
      });
    },

    initRadarChart() {
      const self = this;
      //雷達圖
      this.$nextTick(function () {
        const industryCategory = [`${self.wording.industry}平均`, `您的分數`];
        const careerCategory = [`${self.careerName}業平均`, `您的分數`];

        let dimensionNames = self.dimensionNames;

        let industryScores = self.industryChartWordings.map(
          (chartWording) => chartWording.targetScore
        );

        let careerScores = self.careerChartWordings.map(
          (chartWording) => chartWording.targetScore
        );
        let myScores = self.industryChartWordings.map(
          (chartWording) => chartWording.myScore
        );

        self.industryOption = buildIndustryChartOption(
          industryCategory,
          dimensionNames,
          industryScores,
          myScores
        );

        self.industryPrOption = buildIndustryPrOption(
          self.industryPrs,
          dimensionNames
        );

        self.careerOption = buildCareerChartOption(
          careerCategory,
          dimensionNames,
          careerScores,
          myScores
        );

        self.careerPrOption = buildCareerPrOption(
          self.careerPrs,
          dimensionNames
        );
      });
    },

    initChartContent(answer, question) {
      // 預處理分數，剔除不必要的小數
      this.processScore(answer, question);
      console.log('score json: ', this.scoreJson);

      const self = this;

      // 計算分數
      _.forEach(this.scoreJson, function (value) {
        self.score = floatAdd(self.score, value.score);
      });

      const scoreData = {
        dimensions: _.keys(self.scoreJson),
        score: [],
      };

      _.forEach(scoreData.dimensions, function (value) {
        const dimensionScore = self.scoreJson[value].score;
        scoreData.score.push(dimensionScore);
      });

      console.log('score data: ', scoreData);

      this.initSummaryChart();
      this.initRadarChart();
    },

    initPrValue() {
      console.log('industry pr: ', this.industryPrScores);

      if (!!this.totalCount && !!this.careerName && !!this.industryPrScores) {
        const industryDimensions = this.industryPrScores['生活服務業'];
        const industryScores = industryDimensions.find(
          (dimension) => dimension.dimension === '競爭力總分'
        );
        const industryPrValues = industryScores['pr-values'];

        for (let i = MAX_PR; i >= 0; i--) {
          if (this.totalCount >= industryPrValues[i]) {
            this.industryPr = i;
            break;
          }
        }

        const careerDimensions = this.industryPrScores[this.careerName];
        const careerScores = careerDimensions.find(
          (dimension) => dimension.dimension === '競爭力總分'
        );
        const careerPrValues = careerScores['pr-values'];

        for (let i = careerPrValues.length - 1; i >= 0; i--) {
          if (this.totalCount >= careerPrValues[i]) {
            this.careerPr = i;
            break;
          }
        }
      }
    },
    initDimensionPrValues() {
      if (
        !!this.industryChartWordings &&
        !!this.careerChartWordings &&
        !!this.industryPrScores &&
        !!this.careerName
      ) {
        console.log('industry chart wordings: ', this.industryChartWordings);
        console.log('career chart wordings: ', this.careerChartWordings);

        const industryDimensions = this.industryPrScores['生活服務業'];
        const industryPrs = this.dimensionNames.map((dimName) => {
          const dimScore = this.industryChartWordings.find(
            (dimScr) => dimScr.dimension === dimName
          );
          const dimPrs = industryDimensions.find(
            (dimPr) => dimPr.dimension === dimName
          );
          const dimPrValues = dimPrs['pr-values'];
          for (let i = dimPrValues.length - 1; i >= 0; i--) {
            if (dimScore.myScore >= dimPrValues[i]) {
              return i;
            }
          }

          return 0;
        });

        const careerDimensions = this.industryPrScores[this.careerName];
        const careerPrs = this.dimensionNames.map((dimName) => {
          const dimScore = this.careerChartWordings.find(
            (dimScr) => dimScr.dimension === dimName
          );
          const dimPrs = careerDimensions.find(
            (dimPr) => dimPr.dimension === dimName
          );
          const dimPrValues = dimPrs['pr-values'];
          for (let i = dimPrValues.length - 1; i >= 0; i--) {
            if (dimScore.myScore >= dimPrValues[i]) {
              return i;
            }
          }

          return 0;
        });

        const industryPrWording = [];
        const careerPrWording = [];

        for (let i = 0; i < this.dimensionNames.length; i++) {
          const dimension = this.dimensionNames[i];

          industryPrWording.push({
            dimension,
            prValue: industryPrs[i],
          });

          careerPrWording.push({
            dimension,
            prValue: careerPrs[i],
          });
        }

        console.log('industry prs: ', industryPrs);
        console.log('career prs: ', careerPrs);
        console.log('industry pr wording: ', industryPrWording);
        console.log('career pr wording: ', careerPrWording);

        this.industryPrs = industryPrs;
        this.industryPrWording = industryPrWording;
        this.careerPrs = careerPrs;
        this.careerPrWording = careerPrWording;
      }
    },

    getCareerDimensionAvg(dimensionName) {
      const careerScores = this.industryScores.find(
        (careerScore) => careerScore.name === this.careerName
      );
      const dimensionScore = careerScores.dimensions.find(
        (dimension) => dimension.name === dimensionName
      );
      return parseFloat(dimensionScore.score);
    },

    getCareerDimensionPr(dimensionName, myScore) {
      const careerPrValues = this.industryPrScores[this.careerName];
      const dimensionPrValues = careerPrValues.find(
        (careerPr) => careerPr.dimension === dimensionName
      )['pr-values'];

      for (let i = MAX_PR; i >= 0; i--) {
        const prScore = parseFloat(dimensionPrValues[i]);
        if (myScore >= prScore) {
          return i;
        }
      }

      return 0;
    },

    buildSubDimensionScore(mainDimension, subDimension) {
      const myScore = parseFloat(this.scoreJson[subDimension].score);
      const maxScore = parseFloat(this.scoreJson[subDimension].maxScore);
      const myPercentageScore = calPercentage(myScore, maxScore, 1);

      const careerAvg = this.getCareerDimensionAvg(subDimension);
      const careerPr = this.getCareerDimensionPr(
        subDimension,
        myPercentageScore
      );

      const result = PR_RANGE.find(
        (range) => range.min <= careerPr && careerPr <= range.max
      );

      return {
        main: mainDimension,
        sub: subDimension,
        score: myPercentageScore,
        careerAvg: careerAvg,
        pr: careerPr,
        result: result,
      };
    },

    initSubDimensionScores() {
      console.log('industry pr: ', this.industryPrScores);
      // console.log("score json: ", this.scoreJson);
      // console.log("industry score: ", this.industryScores);

      const subDimensionScores = [];
      const self = this;

      _.forEach(DIMENSION_RELATIONS, function (dimensionRelation) {
        const { main, sub } = dimensionRelation;
        const [sub1, sub2] = sub;

        subDimensionScores.push(self.buildSubDimensionScore(main, sub1));
        subDimensionScores.push(self.buildSubDimensionScore(main, sub2));
      });

      console.log('sub dimension score: ', subDimensionScores);

      this.subDimensionScores = subDimensionScores;
    },
  },
};
</script>

