<template>
  <div id="all_3">
    <!-- /container -->
    <hr />

    <div id="all_2">
      <div class="card">
        <!--form id="form1" class="form-signin" @submit="register"--><!---->
        <div class="card-header">
          編輯基本資料
        </div>
        <div class="card-body">
          <!-- 帳號 -->
          <div class="form-group row">
            <label class="col-sm-2 col-form-label">帳號</label>
            <div class="col-sm-10">
            {{ username }}
            </div>
          </div>
          <br />
          <!-- 密碼 -->
          <div class="form-group row">
            <label class="col-sm-2 col-form-label">密碼</label>
            <div class="col-sm-10">
              <input
                type="password"
                v-model="password"
                pattern="^(?=.*\d)(?=.*[a-z])(?=.*[A-Z]).{8,}$"
                class="form-control"
                placeholder="密碼"
                title="密碼規則至少8碼且有一個數字、小寫英文及大寫英文"
                required
              />
            </div>
          </div>
          <br />

          <div class="form-group row">
            <label class="col-sm-2 col-form-label">再次輸入密碼</label>
            <div class="col-sm-10">
              <input
                type="password"
                v-model="password2"
                pattern="^(?=.*\d)(?=.*[a-z])(?=.*[A-Z]).{8,}$"
                class="form-control"
                placeholder="密碼"
                title="密碼規則至少8碼且有一個數字、小寫英文及大寫英文"
                required
              />
            </div>
          </div>
          <br />

          <div class="form-group row">
            <label class="col-sm-2 col-form-label">會員名稱</label>
            <div class="col-sm-10">
              <input
                type="text"
                v-model="m_name"
                class="form-control"
                placeholder="會員名稱"
                required
              />
            </div>
          </div>
          <br />

          <div class="form-group row">
            <label class="col-sm-2 col-form-label">公司名稱</label>
            <div class="col-sm-10" id="app">
              <input
                type="text"
                v-model.lazy="c_name"
                class="form-control"
                placeholder="公司名稱"
                required
              />
            </div>
          </div>
          <br />

          <div class="form-group row">
            <label class="col-sm-2 col-form-label">統一編號</label>
            <div class="col-sm-10">
              <input
                type="text"
                v-model="c_num"
                class="form-control"
                pattern="\[0-9]{8}"
                placeholder="統一編號"
                title="請輸入統一編號"
                required
              />
            </div>
          </div>
          <br />


           <div class="form-group row">
            <label class="col-sm-2 col-form-label">聯絡信箱</label>
            <div class="col-sm-10">
              <input
                type="email"
                v-model="email"
                pattern="[a-z0-9._%+-]+@[a-z0-9.-]+\.[a-z]{2,}$"
                class="form-control"
                placeholder="電子信箱"
                required
                autofocus
              />
            </div> 
          </div>
        <br />

          <div class="form-group row">
            <label class="col-sm-2 col-form-label">聯絡電話</label>
            <div class="col-sm-10">
              <input
                type="tel"
                v-model="phone"
                class="form-control"
                placeholder="連絡電話"
                required
              />
            </div>
          </div>
          <br />

          <div class="form-group row">
            <label class="col-sm-2 col-form-label">員工人數</label>
            <div class="col-sm-10">
              <select
                class="browser-default custom-select"
                v-model="workers"
                required
              >
                <!-- <option value disabled selected>請選擇員工人數</option> -->
                <option v-for="(item, index) in forms2" :value="item">
                  ({{ index + 1 }}){{ item }}
                </option>
              </select>
            </div>
          </div>
          <br />

          <div class="form-group row">
            <label class="col-sm-2 col-form-label">產業類別</label>
            <div class="col-sm-10">
              <select
                class="browser-default custom-select"
                v-model="industry"
                required
              >
                <option v-for="(item, index) in forms" :value="item">
                  ({{ index + 1 }}){{ item }}
                </option>
              </select>
            </div>
          </div>
          <br />
          <div align="center">
            <!--input type="checkbox" required="required" />我同意
              <br />
              <br /-->
            <button class="btn btn-primary" @click="update">確定</button>
            <router-link class="btn btn-secondary" :to="`/view/view_home`" role="button">回到首頁</router-link>
          </div>
        </div>
        <!--/form-->
      </div>
    </div>
  </div>
</template>

<script>
import $ from "jquery";

export default {
  data() {
    return {
      forms: [
        "餐飲服務業",
        "食品零售業",
        "住宿及旅館業",
        "布疋、皮革及服飾業",
        "家用器具及用品業",
        "汽機車及其零配件業",
        "美髮及美容美體業",
        "運動健康及其器材用品業",
        "文教育樂用品業",
        "休閒娛樂業",
        "生技產品業",
        "不動產業",
        "其他",
      ],
      forms2: [
        "5人以下(不含5人)",
        "5人~10人(含)",
        "11人~50人",
        "51人~100人",
        "101人以上",
      ],
      email: "",
      password: "",
      password2: "",
      m_name: "",
      c_name: "",
      c_num: "",
      phone: "",
      industry: "",
      workers: "",
      username:""
    };
  },
  created() {
    this.read_data();//先執行
  },
  methods: {
    back_to_login() {
      this.$router.push("/view/view_home"); ///view/view_login
    },
    read_data() {
      let password = localStorage.pa;
      let username = localStorage.view_username;
      this.axios
        .post("/rise_api/public/index.php/view/select", {
          username: username,
          password: password,
        })
        .then((response) => {

          console.log(response.data["0"]);
          this.username = response.data["0"]["username"]; //帳號
          this.email = response.data["0"]["email"];
          this.password = response.data["0"]["password"]; //password
          this.password2 = response.data["0"]["password"];
          this.m_name = response.data["0"]["name"]; //會員名稱
          this.c_name = response.data["0"]["cname"]; //公司名稱
          this.c_num = response.data["0"]["cnum"]; //統一編號
          this.phone = response.data["0"]["phone"]; //連絡電話
          this.workers = response.data["0"]["workers"]; //員工人數
          this.industry = response.data["0"]["industry"]; //產業類別
        });
    },
    update() {
      if(this.password==this.password2){
        this.axios
        .put(
          "/rise_api/public/index.php/view/update_data",
          {
            email: this.email,
            password: this.password,
            m_name: this.m_name,
            c_name: this.c_name,
            c_num: this.c_num,
            phone: this.phone,
            workers: this.workers,
            industry: this.industry,
            v_username: localStorage.view_username,
          }
        )
        .then((response) => {

          this.$swal({
            icon: "success",
            title: "儲存成功!",
          });
        });
      }else{
        this.$swal({
            icon: "error",
            text: "密碼與確認密碼不一致!"
          });
      }

    },
  },
};
</script>

<style>
#all_2 {
  width: 90%;
  margin: auto;
}
#all_3 {
  width: 70%;
  margin: auto;
}
.form-signin input[type="email"] {
  /*  border-bottom-right-radius: 0;
  border-bottom-left-radius: 0;*/
  text-indent: 35px;
  background-size: 30px 35px;
  background-image: url("../assets/email.png");
  background-position: 7px 4px;
  background-repeat: no-repeat;
}
.form-signin input[type="password"] {
  /*  border-top-left-radius: 0;
  border-top-right-radius: 0;*/
  text-indent: 35px;
  background-size: 25px 25px;
  background-image: url("../assets/password.png");
  background-position: 9px 9px;
  background-repeat: no-repeat;
}
.bbbox {
  border: 1px solid #cccccc;
  width: auto;
  height: 200px;
  background-color: #ffffff;
  padding: 5px;
  overflow: auto;
}
.myMOUSE:hover {
  cursor: pointer;
  color: #0056c2;
  text-decoration: underline;
}
</style>



