<template>
  <div id="all_3">
    <!-- /container -->
    <hr />
    <div id="all_2">
      <div class="card">
        <div class="card-header">忘記密碼</div>
        <div class="card-body">
          <div class="form-group row">
            <label class="col-sm-2 col-form-label">請輸入新密碼</label>
            <div class="col-sm-10">
              <input v-model="password" class="form-control" type="password" />
            </div>
          </div>
          <div class="form-group row">
            <label class="col-sm-2 col-form-label">再次輸入新密碼</label>
            <div class="col-sm-10">
              <input v-model="password2" class="form-control" type="password"/>
            </div>
          </div>
          <!-- 密碼 -->
          <div align="center">
            <button class="btn btn-primary login_button" @click="password_reset()">送出</button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import $ from "jquery";

export default {
  data() {
    return {
      password: "",
      password2: ""
    };
  },
  created() {
    // this.password_reset();
    this.read_token();
  },
  methods: {
    //近來先確認網址有沒有錯誤(檢查token)
    read_token() {
      let reset_token = location.href.split("password_reset/");
      this.axios
        .post(
          "/rise_api/public/index.php/email/password_check_token",
          {
            token: reset_token[1]
          }
        )
        .then(response => {
          if (response.data == "0") {
            this.$swal({
              icon: "error",
              title: "無此修改密碼請求或連結已過期",
              showConfirmButton: false,
              timer: 2500
            }).then(() => {
              this.$router.push({
                name: "view_home"
              });
            });
          }
          //超過三小時
          else if (response.data > 10800) {
            this.$swal({
              icon: "error",
              title: "此修改密碼請求已過期",
              showConfirmButton: false,
              timer: 2500
            }).then(() => {
              this.$router.push({
                name: "view_home"
              });
            });
          }
          //表示有這個token 而且期限還沒過期
          else {
          }
        });
    },
    //點擊修改密碼按鈕
    password_reset() {
      let reset_token = location.href.split("password_reset/");
      if (this.password == this.password2) {
        if (
          !String(this.password).match(/^(?=.*\d)(?=.*[a-z])(?=.*[A-Z]).{8,}$/)
        ) {
          this.$swal({
            icon: "error",
            text: "密碼規則至少8碼且有一個數字、小寫英文及大寫英文"
          });
        } else {
          this.axios
            .put(
              "/rise_api/public/index.php/email/password_reset",
              {
                token: reset_token[1],
                password: this.password
              }
            )
            .then(response => {
              if (response.data == "1") {
                this.$swal({
                  icon: "success",
                  title: "修改成功",
                  showConfirmButton: false,
                  timer: 1500
                }).then(() => {
                  this.$router.push({
                    name: "view_login"
                    // params: { email: this.email }  //先註解掉 如果加入的話 可使跳轉到登入頁面時 帶入帳號(this.email要再改)
                  });
                });
              } else if (response.data == "0") {
                this.$swal({
                  icon: "error",
                  title: "此帳號已經驗證通過",
                  showConfirmButton: false,
                  timer: 1500
                }).then(() => {
                  this.$router.push({
                    name: "view_login"
                    // params: { email: this.email }  //先註解掉 如果加入的話 可使跳轉到登入頁面時 帶入帳號(this.email要再改)
                  });
                });
              }
              //理論上不會出現
              else {
                this.$swal({
                  icon: "error",
                  title: "驗證錯誤，請聯絡信箱:mitech@iii.org.tw，謝謝。",
                  showConfirmButton: false,
                  timer: 3000
                }).then(() => {
                  this.$router.push({
                    name: "view_home"
                  });
                });
              }
            });
        }
      } else {
        this.$swal({
          icon: "error",
          text: "密碼與確認密碼不一致!"
        });
      }
    }
  }
};
</script>

<style>
#all_2 {
  width: 90%;
  margin: auto;
}
#all_3 {
  width: 70%;
  margin: auto;
}
</style>



