<template>
  <div>
    <div id="all">
<!--     <div class="title">-->
<!--        <img src="../assets/titlenew.jpg" />-->
<!--      </div>-->
      <nav class="navbar navbar-default">
        <div class="navbar-header">
<!--          <a class="navbar-brand" href="./#/view/view_home">RISE</a>-->
        </div>
        <div v-if="sso === 'true'" class="navbar-header">
          <!-- <router-link :to="`login"> -->
          <font color="#007bff" class="navbar-brand2 " v-if="name" v-html="msg"></font>
          <font-awesome-icon v-if="up" icon="id-card" color="#007bff" class="navbar-brand2 " @click="update_data" />
          <font-awesome-icon icon="home"color="#007bff" />
          <font
            color="#007bff"
            v-if="guest"
            class="navbar-brand2 myMOUSE"
            @click="go_answer_history"
          >問卷歷史資料</font>
          <font color="#007bff" v-if="login_type" class="navbar-brand2 myMOUSE" @click="sign_out">登出</font>
          <a v-else="login_type" class="navbar-brand2" href="./#/view/view_login">登入</a>
          <!-- </router-link> -->
          <a v-if="login_type"></a>
          <a v-else="login_type" class="navbar-brand2" href="./#/view/view_register">註冊</a>
          <!-- <a class="navbar-brand2" href="/#/view/view_about">關於</a> -->
        </div>
        <div v-if="preview" class="navbar-header">
          <font color="#007bff" style="font-size: 50px" class="navbar-brand2 ">問卷預覽</font>
        </div>
      </nav>
    </div>
    <div class="container" id="formContainer">
      <!-- Example row of columns -->
      <div class="row" id="row"></div>
    </div>
    <router-view />
  </div>
</template>

<script>
import $ from "jquery";

export default {
  data() {
    return {
      login_type: false,
      password: "12345",
      aa: localStorage.view_username,
      sign_out_url: "",
      msg:"",
      name:false,
      guest:false,
      up:false,
      banner:"",
      sso:"true",
      preview:false,
    };
  },
  created() {

  this.sso = localStorage.sso;
  if(this.sso === undefined){
    this.sso = 'true'
  }
  // console.log(location.hash)
  //   console.log(location.hash.indexOf("view_home"))
  if(location.hash.indexOf("view_home") >= 0 )
  {
    this.sso = 'true'
  }

  if(this.$route.params.preview === "1"){
    this.sso = 'false'
    this.preview = true;
  }
	if(this.$route.params.id != null && this.$route.params.password != null && this.$route.params.formId != null && this.$route.params.ssoFlag === '0'){
      let username = this.$route.params.id;
      let password = this.$route.params.password;
      let customer = this.$route.params.customer;
      this.$store
        .dispatch("view_login", { username, password })
        .then(Response => {
          if (Response.data[0].suc === "T") {
            this.$swal({
              icon: "success",
              title: "登入成功",
              showConfirmButton: false,
              timer: 500
            }).then(() => {
              localStorage.setItem('customer', customer)
              localStorage.setItem('sso', 'false')
              if(this.$route.params.formId !== '0' && this.$route.params.formId !== 'undefined'){
                let formId = this.$route.params.formId;

                this.$router.push("/view/form/"+formId);
                this.$router.go();
              }else{
                this.$router.push("/view/view_home");
                this.$router.go();
              }
            });
          } else {
            this.$swal({
              icon: "error",
              title: "帳號或密碼錯誤",
              showConfirmButton: false,
              timer: 1500
            });
          }
        });
  }else if(this.$route.params.id != null && this.$route.params.password != null && this.$route.params.ssoFlag === '1'){
    let username = this.$route.params.id;
    let password = this.$route.params.password;
    let customer = this.$route.params.customer;


    this.$store
      .dispatch("view_login", { username, password })
      .then(Response => {
        if (Response.data[0].suc === "T") {
          this.$swal({
            icon: "success",
            title: "登入成功",
            showConfirmButton: false,
            timer: 500
          }).then(() => {
            localStorage.setItem('customer', customer)
            localStorage.setItem('sso', 'false')
            if(this.$route.params.formId !== '0' && this.$route.params.time !== '0'){
              let formId = this.$route.params.formId;
              let time = this.$route.params.time;
              this.$router.push("/view/answer_history/"+formId+"/"+time);
              this.$router.go();
            }else{
              this.$router.push("/view/answer_history");
              this.$router.go();
            }
          });
        } else {
          this.$swal({
            icon: "error",
            title: "帳號或密碼錯誤",
            showConfirmButton: false,
            timer: 1500
          });
        }
      });
  }
    this.setBanner();
	  this.login_sta();
    this.sign_out_url = location.href;

  },
  methods: {
    //近來確定有無登入，有登入則不顯測註冊及把登入改為登出
    login_sta() {
      if(localStorage.length==0){
        this.$router.push("/view/view_login"); ///view/view_login
      }
      //console.log(localStorage);
      this.login_type = localStorage.view_username != null ? true : false;
      //判斷是不是登入來決定要不要顯示名字，用三元的寫法跑比較快，頁面比較不會當
      this.name = this.login_type ? true : false;
      this.msg = this.name ? localStorage.uname + " 您好" : "";
      //訪客不需要編輯資料跟問卷歷史資料
      this.guest = this.login_type && localStorage.uname != "guest" ? true :false;
      this.up = this.login_type && localStorage.uname != "guest" ? true :false;

    },
    //登出
    sign_out() {
      let email = localStorage.view_username;
      this.$swal({
        text: "確定要登出嗎?",
        icon: "question",
        showCancelButton: true,
        confirmButtonText: "確定",
        cancelButtonText: "取消",
        preConfirm: () => {
          this.$store.dispatch("view_logout", { email }).then((Response) => {
            localStorage.clear();
            this.$router.push("/view/view_home");
            this.$router.go();
          });
        },
      });
    },
    //編輯基本資料
    update_data(){
      this.$router.push("/view/user_update");
      //去新的頁面可以編輯個人資料
    },
    go_answer_history() {
      this.$router.push("/view/answer_history");
    },
    setBanner(){
      localStorage.setItem('banner', "")
    },
    getBanner(){
      this.banner = localStorage.getItem('banner');
      return this.banner
    },
  },
};
</script>

<style>
body {
  /*background: url("~@/assets/newbg.png") no-repeat;*/
  background-color: #eff0f4;
  background-attachment: fixed;
  background-size: cover;
  font-family: arial,"Microsoft JhengHei","微軟正黑體",sans-serif !important;
}

#all {
  width: 70%;
  margin: auto;
}
img {
  width: 100%;
  max-height: 100%;
}

.navbar {
  font-size: 2rem;
  font-weight: 900;
  position: absolute;
  top: -15px;
  width: 70%;
  margin-bottom: 0px;
  border-radius: 0px;
  border: none;
  background-color: rgba(0, 0, 0, 0);
}

.navbar-default .navbar-brand,
.navbar-default .navbar-nav > li > a {
  color: #5bc5c2;
}
.navbar-header {
  padding-top: 0px;
}

.navbar-brand {
  font-size: 3rem;
  margin-left: 10px;
}

.navbar-brand2 {
  font-size: 1.5rem;
}

#formContainer {
  margin: 0 20px 0 20px;
}

h4 {
  text-align: center;
  font-weight: 1000;
}

a.write {
  font-size: 15px;
  background-color: #ff9e02;
}
#row {
  text-align: center;
}
/* .memo {
  background-color: #ffff95;
  min-height: 150px;
  max-height: 150px;
  max-width: 30%;
  margin: 10px;
  padding: 0 2rem 0 2rem;
  border-radius: 0.8rem;
  box-shadow: 0px 0px 3px #5c5c5c;
  -moz-box-shadow: 0px 0px 3px #5c5c5c;
  -webkit-box-shadow: 0px 0px 3px #5c5c5c;
} */

.col-md-3 {
  text-align: center;
}
/* 因為超連結問題 自製仿超連結的css */
.myMOUSE:hover {
  cursor: pointer;
  color: #0056c2;
  text-decoration: underline;
}
p {
  font-family: Microsoft JhengHei;
}
</style>



