          </select>
        </div>
        <div v-else class="form-group row mr-2" v-for="i in question.choiceList.length">
          <label class="col-sm-1">{{ i }}.</label>
          <select class="form-control col-sm" @change="changea(i-1, $event)" :key="i">
            <option value selected disabled></option>
            <option v-for="(choice, idx) in question.choiceList" :value="idx+1">{{ choice.title }}</option>
          </select>
        </div>
        <div
          v-if="question.hasOther"
          style="display:inline;"
        >
          <span class="text-nowrap_Q">{{ question.choiceList[question.choiceList.length-1].title }}</span>
          <input
            class="form-control"
            style="display:inline;width:200px;border: white;border-bottom:2px solid #00816d;"
            type="text"
            placeholder="其他"
            v-model="question.message"
          />
        </div>
      </div>

      <div v-if="question.type==='text'">
        <input type="text" placeholder="請填入文字" class="form-control" style="border: white;border-bottom:2px solid #00816d; " v-model="answer.ans[0]" />
      </div>

      <div v-if="question.type==='companyName'">
        <input type="text" placeholder="請填入文字" class="form-control" style="border: white;border-bottom:2px solid #00816d; " v-model="company.companyName" />
      </div>

      <div v-if="question.type==='companyNum'">
        <input type="text" placeholder="請填入文字" class="form-control" style="border: white;border-bottom:2px solid #00816d; " v-model="company.companyNum" />
      </div>

      <div v-if="question.type==='class'" style="font-size: 12pt;color: #a6a6a6">
        <span class="text-nowrap_Q">{{ question.remarks }}</span>
      </div>

      <!-- 不會有email跟電話題目 -->
      <div v-if="question.type==='email'">
        <input type="email" placeholder="請填入Email" class="form-control" style="border: white;border-bottom:2px solid #00816d; " v-model="answer.ans[0]" />
      </div>

      <div v-if="question.type==='phone'">
        <input type="phone" placeholder="請填入電話" class="form-control" style="border: white;border-bottom:2px solid #00816d; " v-model="answer.ans[0]" />
      </div>
    </div>
  </div>
</template>


<script>
export default {
  props: ["question", "answer", "order", "number", "questionNumber_checkbox","company","blue_checkbox" ],
  name: "question",
  data() {
    return {
      required: true,

    };
  },
  methods: {
    //單選題處理
    toggleChoice(idx, ans) {
      var find = ans.includes(idx);
      ans.pop();
      if (!find) {
        ans.push(idx);
      }
    },
    //多選題處理
    toggleSelect(idx, ans) {
      var find = ans.indexOf(idx);
      if (find !== -1) {
        ans.splice(find, 1);
      } else {
        ans.push(idx);
        ans.sort();
      }
    },
    //有被選的
    isChoice(idx, ans) {
      return ans.includes(idx);
    },
    //排序下拉選單處理
    changea(idx, e) {
      this.$set(this.answer.ans, idx, e.target.value);
    },
  },
};
</script>

<style scoped>
.text-nowrap_Q{
  overflow-wrap: break-word;
 }
.checkbox,
.radio {
  display: block;
  position: relative;
  cursor: pointer;
  color: #00816d;
  font-size: 16pt;
}
.checkbox-blue,
.radio-blue {
  display: block;
  position: relative;
  cursor: pointer;
  color: #486996;
  font-size: 16pt;
}
input[type="checkbox"],
input[type="radio"] {
  position: absolute;
  right: 9000px;
}
div[o="1"] > .q-title {
  color: #fff;
}
</style>



