<template>
  <section class="answer-conclusion">
    <div class="row">
      <div
        style="--src: url(../f771f86d59f7596838773980fcb59488_violet.png)"
        class="digital-group digital-group-posize"
      >
        <div class="digital-header-posize conclusion-title">
          <span>第四部份 結語</span>
        </div>
      </div>
    </div>

    <div class="row conclusion-panel">
      <div
        class="col-xl-8 col-lg-8 col-md-12 col-sm-12"
        v-if="isSmall()"
      >
        <div class="card-deck">
          <div
            class="card"
            v-for="(conclusion, index) in conclusions"
            :key="index"
          >
            <p class="card-text">{{ conclusion }}</p>
          </div>
        </div>
      </div>
      <div
        class="col-xl-8 col-lg-8 col-md-12 col-sm-12"
        v-else
      >
        <div
          class="text-left conclusion"
          v-for="(conclusion, index) in conclusions"
          :key="index"
        >
          {{ conclusion }}
        </div>
      </div>
      <div class="col-xl-4 col-lg-4 col-md-12 col-sm-12">
        <div
          class="card-deck"
          v-if="isSmall()"
        >
          <div class="card">
            <h3 class="card-title">資策會FIND：</h3>
            <div>
              <p
                class="card-text"
                v-for="(contact, index) in contacts"
                :key="index"
              >
                <a :href="`mailto:${contact.email}`"
                  >{{ contact.title }} <font-awesome-icon icon="envelope"
                /></a>
                <br />
                <a :href="`tel:${contact.tel}`">{{ contact.tel }}</a>
              </p>
            </div>
          </div>
        </div>
        <div
          class="text-left contact"
          v-else
        >
          <span class="contact-title">資策會FIND：</span>
          <br />
          <span
            v-for="(contact, index) in contacts"
            :key="index"
          >
            {{ contact.title }} {{ contact.tel }}
            <br />
            {{ contact.email }}
            <br />
          </span>
        </div>
        <div
          class="card-deck"
          v-if="isSmall()"
        >
          <div
            class="card"
            v-for="(link, index) in links"
            :key="index"
          >
            <h3 class="card-title">{{ link.titleHtml }}</h3>
            <a :href="link.url">
              <qrcode-vue
                class="card-img-top"
                :value="link.url"
              />
            </a>
            <p class="card-text" v-html="link.descHtml"></p>
          </div>
        </div>
        <div v-else>
          <div
            class="text-left link-block"
            v-for="(link, index) in links"
            :key="index"
          >
            <div class="link-qrcode">
              <a :href="link.url">
                <qrcode-vue :value="link.url" />
              </a>
            </div>
            <div class="link-content">
              <span
                class="link-title text-left"
                v-html="link.titleHtml"
              ></span>
              <br />
              <span
                class="text-left link-desc"
                v-html="link.descHtml"
              ></span>
            </div>
          </div>
        </div>
      </div>
    </div>
  </section>
</template>

<script>
import QrcodeVue from 'qrcode.vue';
import { isSmallScreen } from '../../util/window_utils';

export default {
  components: { QrcodeVue },
  props: {
    conclusions: Array,
    links: Array,
    contacts: Array,
  },
  data() {
    return {};
  },
  computed: {},
  created() {},
  methods: {
    isSmall() {
      return isSmallScreen();
    },
  },
};
</script>

<style scoped>
.answer-conclusion {
  padding-left: 10px;
  padding-right: 10px;
  margin-bottom: 50px;
}

.conclusion-title {
  color: #ffffff;
  font-family: inherit;
  font-size: 2.3rem;
  font-weight: 700;
  padding-bottom: 10px;
  white-space: pre-line;
  text-align: left;
}

.conclusion-panel {
  padding-left: 50px;
  padding-right: 50px;
}

.conclusion {
  font-size: 1.5rem;
  padding-left: 20px;
  padding-right: 20px;
  margin-right: 20px;
}

.contact {
  font-size: 1.3rem;
}

.contact-title {
  font-size: 1.3rem;
  font-weight: 700;
}

.link-title {
  font-size: 1.5rem;
  font-weight: 700;
}

.link-desc {
  font-size: 1.5rem;
}

.link-block div {
  float: left;
  clear: none;
  margin-top: 10px;
}

.link-qrcode {
  position: relative;
}

.link-content {
  position: relative;
  padding-left: 10px;
  padding-right: 10px;
}
</style>



