<!--
	前台填寫問卷
-->
<template>
  <div class="form-wrapper container">
    <div class="title">
      <img
        v-if="!form.banner || form.banner.trim() === ''"
        src="../assets/titlenew.jpg"
        style="width: 100%; height: 35%"
      />
      <img
        v-else
        v-bind:src="form.banner"
        style="width: 100%; height: 35%"
      />
      <!--      <h1 style="position:absolute;top:175px;left: 650px;color: #4B0091;">{{form.name}}</h1>-->
    </div>
    <div v-if="isAgree">
      <div v-for="(q, idx) in questions_all[pageNumber]">
        <div v-if="isShow.includes(q.order)">
          <div
            v-if="q.type !== 'group' && q.type !== 'class'"
            class="form-question"
            :class="{ error: isError[q.order] }"
          >
            <question
              :blue_checkbox="form.blue_checkbox"
              :questionNumber_checkbox="form.questionNumber_checkbox"
              :question="q"
              :answer="answers[q.order]"
              :order="q.order"
              :number="q.number"
              :company="company"
            ></question>
          </div>
          <div
            v-else-if="q.type === 'class' && form.blue_checkbox === '1'"
            class="form-question-class-blue"
            :class="{ error: isError[q.order] }"
          >
            <question
              :blue_checkbox="form.blue_checkbox"
              :questionNumber_checkbox="form.questionNumber_checkbox"
              :question="q"
              :answer="answers[q.order]"
              :order="q.order"
              :number="q.number"
              :company="company"
            ></question>
          </div>
          <div
            v-else-if="q.type === 'class' && form.blue_checkbox === '0'"
            class="form-question-class"
            :class="{ error: isError[q.order] }"
          >
            <question
              :blue_checkbox="form.blue_checkbox"
              :questionNumber_checkbox="form.questionNumber_checkbox"
              :question="q"
              :answer="answers[q.order]"
              :order="q.order"
              :number="q.number"
              :company="company"
            ></question>
          </div>
          <div
            v-else
            v-for="(gq, g_idx) in form.groups[q.name]"
            class="form-question"
            :class="{ error: isError[q.order + '-' + gq.order] }"
          >
            <question
              :question="gq"
              :answer="answers[q.order + '-' + gq.order]"
              :order="q.order + '-' + gq.order"
              :number="q.number + '-' + gq.order"
              :questionNumber_checkbox="form.questionNumber_checkbox"
              :blue_checkbox="form.blue_checkbox"
              :company="company"
            ></question>
          </div>
        </div>
      </div>
      <div
        v-if="form.blue_checkbox === '1'"
        class="send"
      >
        <button
          v-if="pageNumber !== 0"
          class="btn btn-primary"
          @click="pagePre"
        >
          上一頁
        </button>
        <button
          v-if="pageNumber !== questions_all.length - 1"
          class="btn btn-primary"
          @click="pageNext"
        >
          下一頁
        </button>
      </div>
      <div
        v-else
        class="send"
      >
        <button
          v-if="pageNumber !== 0"
          class="btn btn-warning"
          @click="pagePre"
        >
          上一頁
        </button>
        <button
          v-if="pageNumber !== questions_all.length - 1"
          class="btn btn-warning"
          @click="pageNext"
        >
          下一頁
        </button>
      </div>
      <div
        v-if="questions_all.length === pageNumber + 1"
        class="send"
      >
        <button
          v-if="form.blue_checkbox === '1'"
          class="btn btn-primary"
          @click="submit"
        >
          送出
        </button>
        <button
          v-else
          class="btn btn-warning"
          @click="submit"
        >
          送出
        </button>
      </div>
    </div>
    <div
      v-else
      id="read-first"
      style="padding: 20px"
    >
      <div v-html="form.first_read"></div>
      <div class="send">
        <div class="my-3">
          <input
            type="checkbox"
            v-model="iAgree"
          />我同意
        </div>
        <div class="my-3">
          <button
            class="btn btn-primary"
            :disabled="!iAgree"
            @click="agree()"
          >
            開始填寫
          </button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import $ from 'jquery';
import Question from './Question.vue';

const getAnswerRoute = (reportType) => {
  let type = parseInt(reportType);

  if (type === 1) {
    return 'answer_1';
  } else if (type === 2) {
    return 'answer_2';
  } else {
    return 'answer';
  }
};

export default {
  components: {
    Question,
  },
  data() {
    return {
      form: '',
      answers: null, //使用者回答
      isAgree: false, //是否同意閱讀前須知
      iAgree: false, //我同意checkbox
      isError: {}, //回答有誤的題號
      now: null, //進入問卷時間
      answers_2: [], //實際上送出分數的部分(使用者回答是answers)
      questions_all: [],
      pageNumber: 0,
      company: { companyName: '', companyNum: '' },
    };
  },
  watch: {
    isAgree() {
      //isAgree變更時
      $('html,body').scrollTop(0);
    },
  },
  mounted() {
    //取上架版本的問卷
    this.axios
      .get('/rise_api/public/index.php/view/getForm/' + this.$route.params.id)
      .then((response) => {
        let _this = this;
        this.form = response.data;
        let questions = [];
        this.form.questions.forEach(function (item) {
          if (item.type !== 'page') {
            questions.push(item);
          } else {
            _this.questions_all.push(questions);
            questions = [];
          }
        });
        _this.questions_all.push(questions);
        this.form.first_read = this.form.first_read.replace(/\n/g, '<br/>'); //處理填寫前須知換行
        this.answers = this.initAnswer; //空的回答
        document.title = this.form.name;
      });
  },
  methods: {
    agree() {
      this.isAgree = true;
      this.now = new Date(); //紀錄開始填寫時間
      history.pushState(null, null, document.URL);
      window.addEventListener('popstate', function () {
        history.pushState(null, null, document.URL);
        console.log('不能回上一頁');
      });
    },
    //檢查使用者回答
    checkAnswer(question, order, number) {
      var self = this;
      var ans = self.answers[order].ans; //使用者回答
      var message = ''; //錯誤訊息

      if (self.form.questionNumber_checkbox === '0') {
        number = question.title;
      }

      // 該題必填 & 回答為空
      if (
        question.required === true &&
        ans.length === 0 &&
        question.type !== 'companyName' &&
        question.type !== 'companyNum'
      ) {
        self.isError[order] = true;
        message += `<p>${number}. 為必選題</p>`;
      } else if (
        ans.length !== 0 ||
        question.type === 'companyName' ||
        question.type === 'companyNum'
      ) {
        //題型檢查
        switch (question.type) {
          // 已經沒有電子郵件的題型了
          case 'email':
            var emailRule =
              /^\w+((-\w+)|(\.\w+))*\@[A-Za-z0-9]+((\.|-)[A-Za-z0-9]+)*\.[A-Za-z]+$/;
            if (ans[0].search(emailRule) < 0) {
              self.isError[order] = true;
              message += order + '. 請確認Email格式\n';
            }
            break;
          // 已經沒有電話的題型了
          case 'phone':
            var telRule1 = /^\(?\d{2}\)?\-?\d{3,4}\-?\d{4}#?\d*?$/; //市話
            var telRule2 = /^\(?\d{4}\)?\-?\d{3}\-?\d{3}$/; //手機
            if (ans[0].search(telRule1) < 0 && ans[0].search(telRule2) < 0) {
              self.isError[order] = true;
              message += order + '. 請確認電話格式\n';
            }
            break;
          //number改為 input的正常來說會有範圍內的值
          case 'number':
            if (question.manualInput == true) {
              var min = question.min;
              var max = question.max;
              if (ans[0].toString().indexOf('.') !== -1) {
                var ansArray = ans[0].split('.');
                var decimalPlace = ansArray[1].length;
                if (
                  question.decimalPlace !== null &&
                  question.decimalPlace !== undefined &&
                  question.decimalPlace !== '0' &&
                  question.decimalPlace.trim() !== ''
                ) {
                  if (decimalPlace.toString() !== question.decimalPlace) {
                    self.isError[order] = true;
                    message += `<p>${number}. 請輸入小數點位到第${question.decimalPlace}位</p>`;
                  }
                } else {
                  self.isError[order] = true;
                  message += `<p>${number}. 請輸入整數</p>`;
                }
              } else {
                if (
                  question.decimalPlace !== null &&
                  question.decimalPlace !== undefined &&
                  question.decimalPlace !== '0' &&
                  question.decimalPlace.trim() !== ''
                ) {
                  self.isError[order] = true;
                  message += `<p>${number}. 請輸入小數點位到第${question.decimalPlace}位</p>`;
                }
              }

              if (ans[0] > parseInt(max)) {
                self.isError[order] = true;
                if (question.unit !== null) {
                  message += `<p>${number}. 數字範圍大於最大值 (${max}${question.unit})</p>`;
                } else {
                  message += `<p>${number}. 數字範圍大於最大值 (${max})</p>`;
                }
              }
              if (ans[0] < parseInt(min)) {
                self.isError[order] = true;
                if (question.unit !== null) {
                  message += `<p>${number}. 數字範圍小於最小值 (${min}${question.unit})</p>`;
                } else {
                  message += `<p>${number}. 數字範圍小於最小值 (${min})</p>`;
                }
              }
            }

            break;
          //百分比
          case 'percentage':
            var num = 0;
            ans.forEach(function (a) {
              num += parseInt(a);
            });
            if (num !== 100) {
              self.isError[order] = true;
              message += `<p>${number}. 加總必須為100</p>`;
            }
            break;
          //排序
          case 'priority':
            var hash = {};
            var duplicate = false; //是否重複排序
            ans.forEach(function (a) {
              if (hash[a]) {
                self.isError[order] = true;
                duplicate = true;
              } else hash[a] = true;
            });
            if (duplicate) {
              //重複排序
              message += `<p>${number}. 排序重複</p>`;
            } else if (
              ans.filter(function (a) {
                return a !== null;
              }).length !== parseInt(question.maxSelect)
            ) {
              //排序有缺
              self.isError[order] = true;
              message += `<p>${number}. 排序不完整</p>`;
            }
            break;
          //排序
          case 'selection':
            if (question.maxSelect !== '∞') {
              if (ans.length > parseInt(question.maxSelect)) {
                self.isError[order] = true;
                message += `<p>${number}. 選項超過${question.maxSelect}個</p>`;
              }
            }
            break;
          case 'text':
            if (question.required === true && ans[0].trim() === '') {
              self.isError[order] = true;
              message += `<p>${number}.不可為空</p>`;
            }
            break;
          case 'companyName':
            if (
              question.required === true &&
              this.company.companyName.trim() === ''
            ) {
              self.isError[order] = true;
              message += `<p>${number}.不可為空</p>`;
            }
            break;
          case 'companyNum':
            if (
              question.required === true &&
              this.company.companyNum.trim() === ''
            ) {
              self.isError[order] = true;
              message += `<p>${number}.不可為空</p>`;
            }
            break;
          default:
            break;
        }
      }
      return message;
    },
    //計算題目分數，目前僅單選、多選、數字題目計算
    calcScore(question, order) {
      var self = this;
      var ans = self.answers[order].ans; //回答
      var score = 0;
      //表示其他選項有打勾
      if (
        question.hasOther &&
        self.answers[order].ans[0] == question.choiceList.length
      ) {
        this.answers[order].message = question.message;
      }
      if (ans.length > 0) {
        switch (question.type) {
          case 'choice':
            score = parseFloat(question.choiceList[ans[0] - 1].score);
            break;
          case 'selection':
            ans.forEach(function (a) {
              score += parseFloat(question.choiceList[a - 1].score);
            });
            break;
          case 'number':
            score = ans[0];
            break;
          // case 'percentage':
          // 	break;
          // case 'priority':
          // 	break;
        }
        score = Math.min(parseFloat(question.maxScore), score);
      }
      return score;
    },
    //計算題目分數，目前僅單選、多選、數字題目計算
    calcMaxScore(question, order) {
      var self = this;
      var ans = self.answers[order].ans; //回答
      var score = parseFloat(question.maxScore);
      //表示其他選項有打勾

      return score;
    },
    //點擊送出
    submit() {
      var self = this;
      var qt = self.form.questions;
      var gp = self.form.groups;

      //"有顯示"的每一題送去檢查
      self.isError = {};
      var errorMessage = '';
      qt.forEach(function (q1) {
        if (self.isShow.includes(q1.order)) {
          //有顯示的題目
          if (q1.type === 'group') {
            gp[q1.name].forEach(function (q2) {
              errorMessage += self.checkAnswer(
                q2,
                q1.order + '-' + q2.order,
                q1.number + '-' + q2.order
              );
            });
          } else {
            errorMessage += self.checkAnswer(q1, q1.order, q1.number);
          }
        }
      });

      if (errorMessage !== '') {
        //回答有誤
        // $("html,body").animate({ scrollTop: 300 }, 200);
        errorMessage += `<h3>請將題目紅字的內容進行修正後，再進行送出，謝謝。</h3>`;
        this.$swal({
          icon: 'error',
          html: errorMessage,
        }).then((result) => {
          if (result.value) {
            // $("html,body").animate({ scrollTop: 300 }, 200);
          }
        });
      } else {
        //回答沒問題
        //計算構面分數
        var dim = self.form.dimensions; //構面設定
        // var dim = JSON.parse(self.form.final_score).dimensions; //構面設定
        var score = {}; //各構面分數

        //初始化構面分數
        Object.keys(dim).forEach(function (key) {
          score[key] = {};
          score[key].score = 0;
          score[key].maxScore = 0;
          score[key].des = '';
        });

        //各題分數加總至所屬構面
        qt.forEach(function (q1) {
          if (self.isShow.includes(q1.order)) {
            //有顯示的題目
            if (q1.type === 'group') {
              //展開題組
              self.form.groups[q1.name].forEach(function (q2) {
                let self_answers = self.answers[q1.order + '-' + q2.order];

                //表示其他選項有打勾&&選的答案跟答案長度一樣 ==>message選項填入//
                if (q2.hasOther && self_answers.ans == q2.choiceList.length) {
                  self_answers.message = q2.message;
                }
                //表示其他選項有打勾&&選的答案跟答案長度一樣 ==>message選項填入//

                //計算構面的加總分數
                Object.keys(dim).forEach(function (key) {
                  if (dim[key].order.includes(q1.order + '-' + q2.order)) {
                    //如果該構面包含該題
                    score[key].score = self.floatAdd(
                      score[key].score,
                      self.calcScore(q2, q1.order + '-' + q2.order)
                    ); //加總分數
                    score[key].maxScore = self.floatAdd(
                      score[key].maxScore,
                      self.calcMaxScore(q2, q1.order + '-' + q2.order)
                    ); //加總分數
                  }
                });
              });
            } else {
              let self_answers = self.answers[q1.order];

              //多選題-表示其他選項有打勾&&選的答案跟答案長度一樣 ==>message選項填入//
              if (q1.hasOther && q1.type === 'selection') {
                self_answers.message = q1.message;
              }
              //單選題-表示其他選項有打勾&&選的答案跟答案長度一樣 ==>message選項填入//
              if (q1.hasOther && self_answers.ans == q1.choiceList.length) {
                self_answers.message = q1.message;
              }

              //排序題
              if (q1.hasOther && q1.type === 'priority') {
                self_answers.message = q1.message;
              }

              //計算構面的加總分數
              Object.keys(dim).forEach(function (key) {
                if (dim[key].order.includes(q1.order)) {
                  //如果該構面包含該題
                  score[key].score += self.calcScore(q1, q1.order); //加總分數
                  score[key].maxScore += self.calcMaxScore(q1, q1.order); //加總分數
                }
              });
            }
          }
        });

        //構面級距說明
        Object.keys(dim).forEach(function (key) {
          dim[key].level.forEach(function (l) {
            if (
              parseFloat(score[key].score) >= parseFloat(l.min) &&
              parseFloat(score[key].score) <= parseFloat(l.max)
            ) {
              score[key].des = l.des;
            }
          });
        });
        // 送出回答
        this.axios
          .post(`/rise_api/public/index.php/view/answer`, {
            username:
              localStorage.getItem('view_username') !== undefined &&
              localStorage.getItem('view_username') !== null
                ? localStorage.getItem('view_username')
                : 'Guest',
            form_id: this.form.id,
            version: this.form.version,
            answer: this.answers_2,
            companyName: this.company.companyName,
            companyNum: this.company.companyNum,
            score: score,
            timer: Math.round((new Date() - this.now) / 1000), //填寫花費時間(s)
            customer: localStorage.getItem('customer'),
          })
          .then((response) => {
            console.log('response: ', response);
            
            document.body.scrollTop = 0;
            document.documentElement.scrollTop = 0;
            this.$router.push({
              name: `${getAnswerRoute(this.form.report_type)}`,
              // name: "answer",
              params: {
                form_id: this.form.id,
                username:
                  localStorage.getItem('view_username') !== undefined &&
                  localStorage.getItem('view_username') !== null
                    ? localStorage.getItem('view_username')
                    : 'Guest',
                score: score,
                answer: this.answers,
              },
            });
          });
      }
    },
    floatAdd(arg1, arg2) {
      var r1, r2, m;
      try {
        r1 = arg1.toString().split('.')[1].length;
      } catch (e) {
        r1 = 0;
      }
      try {
        r2 = arg2.toString().split('.')[1].length;
      } catch (e) {
        r2 = 0;
      }
      m = Math.pow(10, Math.max(r1, r2));
      return Math.round(arg1 * m + arg2 * m) / m;
    },
    pagePre() {
      this.pageNumber -= 1;
      document.body.scrollTop = 0;
      document.documentElement.scrollTop = 0;
    },
    pageNext() {
      var self = this;
      var qt = self.questions_all[self.pageNumber];
      var gp = self.form.groups;

      //"有顯示"的每一題送去檢查
      self.isError = {};
      var errorMessage = '';
      qt.forEach(function (q1) {
        if (self.isShow.includes(q1.order)) {
          //有顯示的題目
          if (q1.type === 'group') {
            gp[q1.name].forEach(function (q2) {
              errorMessage += self.checkAnswer(
                q2,
                q1.order + '-' + q2.order,
                q1.number + '-' + q2.order
              );
            });
          } else {
            errorMessage += self.checkAnswer(q1, q1.order, q1.number);
          }
        }
      });

      if (errorMessage !== '') {
        //回答有誤
        // $("html,body").animate({ scrollTop: 300 }, 200);
        errorMessage += `<h3>請將題目紅字的內容進行修正後，再前往下一頁，謝謝。</h3>`;
        this.$swal({
          icon: 'error',
          html: errorMessage,
        }).then((result) => {
          if (result.value) {
            // $("html,body").animate({ scrollTop: 300 }, 200);
          }
        });
      } else {
        this.pageNumber += 1;
        document.body.scrollTop = 0;
        document.documentElement.scrollTop = 0;
      }
    },
  },
  computed: {
    //初始化回答(空回答)
    initAnswer() {
      var self = this;
      var order_test = {};
      var answers = {};
      let test_str = this.answers_2;

      self.form.questions.forEach(function (q1) {
        if (q1.type === 'group') {
          //把題組裡的題目拿出來
          self.form.groups[q1.name].forEach(function (q2) {
            var order = q1.order + '-' + q2.order;
            if (q2.type == 'priority' || q2.type == 'percentage') {
              answers[order] = {
                type: q2.type,
                ans: [],
                count: q2.choiceList.length, //為了給後台的匯出EXCEL使用的
              };
            } else {
              answers[order] = {
                type: q2.type,
                ans: [],
              };
            }
            test_str.push({ [order]: answers[order] });
          });
        } else {
          // 一般題目
          var order = q1.order;
          // 分數題目 因為是水平分數軸 要給最大最小值
          if (q1.type === 'number' && q1.manualInput === false) {
            answers[order] = {
              type: q1.type,
              ans: [0],
              max: q1.max,
              min: q1.min,
            };
          } else if (q1.type === 'number' && q1.manualInput === true) {
            answers[order] = {
              type: q1.type,
              ans: [],
              max: q1.max,
              min: q1.min,
            };
          } else if (q1.hasOther) {
            if (q1.type === 'priority') {
              answers[order] = {
                type: q1.type,
                ans: [],
                message: '',
                count: q1.choiceList.length,
              };
            } else {
              answers[order] = {
                type: q1.type,
                ans: [],
                message: '',
              };
            }
          } else if (q1.type == 'priority' || q1.type == 'percentage') {
            answers[order] = {
              type: q1.type,
              ans: [],
              count: q1.choiceList.length, //為了給後台的匯出EXCEL使用的
            };
          } else {
            answers[order] = {
              type: q1.type,
              ans: [],
            };
          }
          test_str.push({ [order]: answers[order] });
        }
      });
      return answers;
    },

    //要顯示的題目
    isShow() {
      var self = this;
      var show = []; //要顯示的題目
      var hide = []; //不顯示的題目
      var group_order = {};

      //找到題組對應的題號
      self.form.questions.forEach(function (q) {
        if (q.type === 'group') {
          group_order[q.name] = q.order;
        }
      });

      //確認那些題目要關閉
      self.form.questions.forEach(function (q) {
        if (q.type === 'choice' || q.type === 'selection') {
          q.choiceList.forEach(function (choice, index) {
            var ans = self.answers[q.order].ans;
            if (choice.group !== '') {
              //if 選項有設定題組
              if (!ans.includes(index + 1))
                //if 題組選項沒有被選
                hide.push(group_order[choice.group]); //隱藏題組
            }
            if (choice.jump !== '') {
              //if 選項有設定跳題
              if (ans.includes(index + 1))
                //if 跳題選項被選
                for (var i = q.order + 1; i < choice.jump; i++) {
                  hide.push(i); //隱藏題目
                }
            }
          });
        }
      });

      //要顯示的題目
      self.form.questions.forEach(function (q) {
        if (!hide.includes(q.order)) {
          //過濾不顯示的題目
          show.push(q.order);
        }
      });
      return show;
    },
  },
};
</script>

<style>
.form-wrapper {
  padding: 0;
  background-color: white;
  width: 75%;
}
.form-question {
  padding: 20px;
  border-bottom: 2px dashed #bbb;
}
.form-question-class {
  padding: 20px;
  /*border-bottom: 2px dashed #bbb;*/
  border-left: 7px solid #00816d;
}
.form-question-class-blue {
  padding: 20px;
  /*border-bottom: 2px dashed #bbb;*/
  border-left: 7px solid #486996;
}
.send {
  margin: 10px 0;
  text-align: center;
}
.error .q-title {
  color: #f05050;
}
#read-first {
  padding: 0 15%;
}
</style>



