const FIX_X = 595.0;
const FIX_Y = 842.0;

const calImgXY = (pdfX, pdfY) => {
  let ratioX = FIX_X / pdfX;
  let ratioY = FIX_Y / pdfY;

  if (ratioX <= ratioY) {
    return [FIX_X, pdfY * ratioX];
  } else {
    return [pdfX * ratioY, FIX_Y];
  }
};

const generatePage = (companyName, formName, pdf, pageCount, domList) => {
  if (_.isEmpty(domList)) {
    pdf.save(companyName + ' ' + formName + '總評.pdf');
  } else {
    const [first, ...rest] = domList;

    html2canvas(first, {
      allowTaint: true,
      useCORS: true,
      scale: 2, // 提升画面质量，但是会增加文件大小
    }).then(function (canvas) {
      // 得到canvas画布的单位是px 像素单位
      const contentWidth = canvas.width;
      const contentHeight = canvas.height;

      // 将canvas转为base64图片
      const pageData = canvas.toDataURL('image/jpeg', 1.0);

      const pdfX = ((contentWidth + 10) / 2) * 0.75;
      const pdfY = (contentHeight / 2) * 0.75;

      const [imgX, imgY] = calImgXY(pdfX, pdfY);

      // var imgX = pdfX;
      // var imgX = 595;
      // var imgY = pdfY * (imgX / pdfX);
      // var imgY = 842;

      // console.log("page: ", pageCount);
      // console.log("contentWidth: ", contentWidth);
      // console.log("contentHeight: ", contentHeight);
      // console.log("pdfX: ", pdfX);
      // console.log("pdfY: ", pdfY);
      // console.log("imgX: ", imgX);
      // console.log("imgY: ", imgY);

      if (pageCount > 1) {
        pdf.addPage('a4', 'portrait');
      }

      pdf.addImage(pageData, 'jpeg', 0, 0, imgX, imgY);

      generatePage(companyName, formName, pdf, pageCount + 1, rest);
    });
  }
};

export const generatePdf2 = (companyName, formName) => {
  let domList = [];
  let pageCount = 1;

  for (let i = 0; i < ANSWER_IDS.length; i++) {
    domList.push(document.getElementById(ANSWER_IDS[i]));
  }

  let pdf = new jsPDF('portrait', 'pt', 'a4', false);

  generatePage(companyName, formName, pdf, pageCount, domList);
};

export const generatePdf = (companyName, formName) => {
  html2canvas(document.body, {
    allowTaint: true,
    useCORS: true,
    scale: 2, // 提升画面质量，但是会增加文件大小
  }).then(function (canvas) {
    // 得到canvas画布的单位是px 像素单位
    var contentWidth = canvas.width;
    var contentHeight = canvas.height;

    console.log('contentWidth', contentWidth);
    console.log('contentHeight', contentHeight);
    // 将canvas转为base64图片
    var pageData = canvas.toDataURL('image/jpeg', 1.0);

    // 设置pdf的尺寸，pdf要使用pt单位 已知 1pt/1px = 0.75   pt = (px/scale)* 0.75
    // 2为上面的scale 缩放了2倍
    var pdfX = ((contentWidth + 10) / 2) * 0.75;
    // var pdfX = (contentWidth * 0.75);
    var pdfY = (contentHeight / 2) * 0.75; // 500为底部留白
    // var pdfY = (contentHeight * 0.75); // 500为底部留白

    // 设置内容图片的尺寸，img是pt单位
    var imgX = pdfX;
    var imgY = (contentHeight / 2) * 0.75; //内容图片这里不需要留白的距离
    // var imgY = (contentHeight * 0.75); //内容图片这里不需要留白的距离

    console.log('pdf x:', pdfX);
    console.log('pdf y:', pdfY);
    console.log('img x:', imgX);
    console.log('img y:', imgY);

    // 初始化jspdf 第一个参数方向：默认''时为纵向，第二个参数设置pdf内容图片使用的长度单位为pt，第三个参数为PDF的大小，单位是pt
    var PDF = new jsPDF('', 'pt', [pdfX, pdfY]);
    // var PDF = new jsPDF("p", "pt", "a4");

    // 将内容图片添加到pdf中，因为内容宽高和pdf宽高一样，就只需要一页，位置就是 0,0
    // PDF.addImage(pageData, "jpeg", 0, 0, imgX, imgY);
    PDF.addImage(pageData, 'jpeg', 0, 0, imgX, imgY);
    PDF.save(companyName + ' ' + formName + '總評.pdf');
  });
};

