export const generatePdf5 = (companyName, formName) => {
  let fileName = companyName + ' ' + formName + '- 總評.pdf';

  html2canvas(document.body, {
    allowTaint: true,
    useCORS: true,
    scale: 2,
    x: X_FIX,
    width: WIDTH,
  }).then(function (canvas) {
    const width = canvas.width;
    const height = canvas.height;
    const pdfOpt = {
      orientation: 'p',
      unit: 'pt',
      format: [width, height],
    };
    const doc = new jsPDF(pdfOpt);

    const imgData = canvas.toDataURL('image/jpg');
    doc.addImage(imgData, 'jpeg', 0, 0, width, height);

    doc.save(fileName);
  });
};

const saveCanvas = (canvas, fileName) => {
  var img = canvas.toDataURL('image/jpeg');
  var link = document.createElement('a');
  link.download = fileName;
  link.href = img;
  link.click();
};

const WIDTH = 1140;
const SCALE = 2;
const X_FIX = 390;
const HEIGHT = 3160;
const HEIGHTS = [2450, 3000, 3000, 3160];

const generatePdf3Work = (
  bp,
  diff,
  index,
  maxWidth,
  maxHeight,
  rest,
  PDF,
  fileName
) => {
  html2canvas(document.body, {
    allowTaint: true,
    useCORS: true,
    scale: SCALE,
    x: X_FIX,
    width: WIDTH,
    y: bp,
    height: !_.isEmpty(rest) ? diff : maxHeight - bp,
  }).then((canvas) => {
    const pageData = canvas.toDataURL('image/jpeg');

    let contentWidth = canvas.width;
    let contentHeight = canvas.height;

    if (index === 0) {
      // contentHeight = contentWidth + 100;

      const pdfOpt = {
        orientation: 'p',
        unit: 'pt',
        // format: [contentWidth, HEIGHTS[index]],
        format: [contentWidth, HEIGHT],
      };
      PDF = new jsPDF(pdfOpt);
    } else {
      // PDF.addPage([contentWidth, HEIGHTS[index]]);
      PDF.addPage([contentWidth, HEIGHT]);
    }

    PDF.addImage(pageData, 'jpeg', 0, 0, contentWidth, contentHeight);

    if (_.isEmpty(rest)) {
      PDF.save(fileName);
    } else {
      const newBp = rest[0];
      const newRest = rest.slice(1);
      const newDiff = !_.isEmpty(newRest)
        ? newRest[0] - newBp
        : maxHeight - newBp;

      return generatePdf3Work(
        newBp,
        newDiff,
        index + 1,
        maxWidth,
        maxHeight,
        newRest,
        PDF,
        fileName
      );
    }
  });
};

const getPageBreak = async () => {
  const result = await fetch('/rise_front/static/bp.json');
  return result.json();
};

export const generatePdf3 = async (companyName, formName) => {
  let fileName = companyName + ' ' + formName + '- 總評.pdf';

  const browserWidth = window.innerWidth;
  console.log('Browser width: ' + browserWidth);

  if (browserWidth < 1920) {
    // return generateJpgAll(companyName, formName);
    return generatePdf4(companyName, formName);
  }

  let pageBreak = await getPageBreak();
  console.log('page break: ', pageBreak);

  /*
  let pageBreak = PAGE_BREAK;
  */

  let initBp = pageBreak[0];
  let rest = pageBreak.slice(1);
  let diff = rest[0] - initBp;

  html2canvas(document.body, {
    allowTaint: true,
    useCORS: true,
    scale: 1,
    x: X_FIX,
    width: WIDTH,
  }).then(function (canvas) {
    var maxWidth = canvas.width;
    var maxHeight = canvas.height;

    console.log('max width: ', maxWidth, ', max height: ', maxHeight);

    generatePdf3Work(initBp, diff, 0, maxWidth, maxHeight, rest, {}, fileName);
  });
};

export const parseWording = (properties) => {
  console.log('properties: ', properties);

  if (
    isEmptyValue(properties) ||
    isEmptyValue(properties.wording) ||
    isEmptyValue(properties.wording.industry)
  ) {
    return {
      industry: '產業',
      higher: '高',
      equal: '相等',
      lower: '低',
    };
  } else {
    return properties.wording;
  }
};

export const parseIndustryScores = (properties) => {
  if (_.isNil(properties) || _.isNil(properties.industryScores)) {
    return [];
  } else {
    console.log('orig industry score: ', properties.industryScores);
    // return eval(JSON.parse(properties.industryScores));
    return properties.industryScores;
  }
};

export const parseCareer = (answerRaw) => {
  if (_.isNil(answerRaw) || _.isNil(answerRaw.answer)) {
    return '';
  } else {
    let answer = JSON.parse(answerRaw.answer);
    let career = '同業';

    for (const ans of answer) {
      let answerKey = Object.keys(ans)[0];
      let answerContent = ans[answerKey];
      if (answerContent['type'] === 'career') {
        career = ans[answerKey]['ans'][0]['title'];
      }
    }

    return career;
  }
};

export const getCareerScore = (industryScores, careerName, dimensionName) => {
  if (_.isNil(industryScores) || _.isEmpty(industryScores)) {
    return null;
  }

  for (const careerScore of industryScores) {
    if (careerScore['name'] === careerName) {
      for (const dimensionScore of careerScore.dimensions) {
        if (dimensionScore['name'] === dimensionName) {
          return dimensionScore.score;
        }
      }
    }
  }

  return null;
};

export const sumCareerScore = (industryScores, careerName) => {
  if (_.isNil(industryScores) || _.isEmpty(industryScores)) {
    return null;
  }

  let industryScoresObj = industryScores;
  while (_.isString(industryScoresObj)) {
    industryScoresObj = JSON.parse(industryScoresObj);
  }

  for (const careerScore of industryScoresObj) {
    if (careerScore['name'] === careerName) {
      let sum = 0.0;

      for (const dimensionScore of careerScore['dimensions']) {
        sum += parseFloat(dimensionScore['score']);
      }

      return sum;
    }
  }

  return null;
};

export const processChart = (chartJson, industryScores, careerName) => {
  let chartObj = JSON.parse(chartJson);
  console.log('chart obj: ', chartObj);

  if (_.isNil(industryScores) || _.isEmpty(industryScores)) {
    return chartObj;
  }

  for (const careerScore of industryScores) {
    if (careerScore['name'] === careerName) {
      for (const dimensionScore of careerScore.dimensions) {
        for (const chartDimension of chartObj.dimensions) {
          let chartDimName = chartDimension['subjectArr'][0];
          let scoreDimName = dimensionScore['name'];
          if (chartDimName === scoreDimName) {
            let dimScore = dimensionScore.score;
            chartDimension['historyArr2'] = dimScore.toString();
          }
        }
      }
    }
  }

  return chartObj;
};

export const calPercentage = (value, max, round) => {
  const q = parseFloat(value) / parseFloat(max);
  return parseFloat((q * 100.0).toFixed(round));
};

export const initChartWording = (
  industryScores,
  careerName,
  dimensionNames
) => {
  const chartWording = [];

  const industryScore = industryScores.find(
    (industryScore) => industryScore.name === careerName
  );

  if (!!industryScore) {
    _.forEach(dimensionNames, (dimensionName) => {
      const dimensionScore = industryScore.dimensions.find(
        (dimension) => dimension.name === dimensionName
      );

      if (!!dimensionScore) {
        chartWording.push({
          dimension: dimensionName,
          targetScore: parseFloat(dimensionScore.score),
          myScore: 0,
          maxScore: 0,
        });
      }
    });
  }

  return chartWording;
};

export const splitChart = (chartData, chart) => {
  return {
    data: chartData,
    type: chart.type,
    description: chart.description,
    title: chart.title,
  };
};

export const buildChart = (vm, idx, chartData, chartFormName, chartName) => {
  // console.log("value: ", value);
  console.log(`chart form name - ${chartFormName}: `, vm[chartFormName]);

  let chartForm = [];

  _.forEach(chartData.data.dimensions, function (dimension, idx2) {
    let dimensionName = dimension.subjectArr[0];
    let careerScore =
      getCareerScore(vm.industryScores, vm.careerName, dimensionName) ||
      dimension.historyArr2;
    const chartFormObj = {
      historyArr: parseFloat(dimension.historyArr),
      historyArr2: parseFloat(careerScore),
      subjectArr: dimensionName,
      myscoreArr: 0,
      chartIndex: idx,
    };

    chartForm.push(chartFormObj);
    vm[chartFormName].push(chartFormObj);
  });

  vm[chartName] = {
    chartJson: chartData,
    chartForm: chartForm,
  };
};

