const splitWithNewLine = (txt, length) => {
  if(txt.length > length){
    const head = txt.substring(0, length);
    const tail = txt.substring(length, txt.length);

    return head + '\n' + splitWithNewLine(tail, length);
  }else{
    return txt;
  }
}

