export const buildSummaryChartOption = (
  categories,
  maxScore = 100,
  totalCount,
  fullMarksContent,
  careerMarksContent
) => {
  console.log(`category: ${categories}`);
  console.log('full marks: ', fullMarksContent);
  console.log('career marks: ', careerMarksContent);

  let xAxis = {};
  let yAxis = {};
  let position = '';

  position = totalCount <= 90 ? 'right' : 'insideRight';

  xAxis = {
    type: 'value',
    max: maxScore,
  };

  yAxis = {
    type: 'category',
    data: categories,
    axisTick: {
      alignWithLabel: true,
    },
  };

  if (isSmallScreen()) {
    position = 'top';

    yAxis = {
      type: 'value',
      max: maxScore,
    };

    xAxis = {
      type: 'category',
      data: categories,
      axisTick: {
        alignWithLabel: true,
      },
      axisLabel: {
        show: true,
        interval: 0,
        rotate: 75,
      },
    };
  }

  return {
    tooltip: {
      trigger: 'axis',
      axisPointer: {
        type: 'shadow',
      },
      extraCssText: 'width:200px; white-space:pre-wrap;',
    },
    grid: {
      left: '3%',
      right: '4%',
      bottom: '3%',
      containLabel: true,
    },
    xAxis: xAxis,
    yAxis: yAxis,
    series: [
      {
        name: 'score',
        barWidth: '60%',
        data: [
          {
            value: careerMarksContent,
            itemStyle: {
              color: OTHER_COLOR,
            },
          },
          {
            value: fullMarksContent,
            itemStyle: {
              color: OTHER_COLOR,
            },
          },
          {
            value: totalCount,
            itemStyle: {
              color: YOUR_COLOR,
            },
          },
        ],
        type: 'bar',
        label: {
          normal: {
            show: true,
            position: position,
            fontSize: 14,
          },
        },
      },
    ],
  };
};

export const floatAdd = (arg1, arg2) => {
  var r1, r2, m;
  try {
    r1 = arg1.toString().split('.')[1].length;
  } catch (e) {
    r1 = 0;
  }
  try {
    r2 = arg2.toString().split('.')[1].length;
  } catch (e) {
    r2 = 0;
  }
  m = Math.pow(10, Math.max(r1, r2));
  return Math.round(arg1 * m + arg2 * m) / m;
};

//比對總構面和有列入圖表的構面 有一樣的填入分數(供後面顯示使用)
export const setScore = (source, target) => {
  _.forEach(target, function (t) {
    const index = source.dimensions.findIndex(
      (dimension) => dimension === t.subjectArr
    );
    if (index === -1) return;
    t.myscoreArr = source.score[index];
  });
};



