<template>
  <section
    class="digital-part3 digital-part3-posize"
    v-if="sectionThreeCheckbox"
  >
    <div v-if="suggestCheckbox">
      <div class="digital-flexbox12 digital-flexbox12-posize">
        <div class="digital-flexbox12-item">
          <div
            style="--src:url(../f4d1a35375a159400b4b6f1ae37af4c4_violet.png)"
            class="digital-group digital-group-posize3"
          >
            <div
              class="digital-title digital-title-posize2"
              style="white-space: pre-line;text-align: left;"
            >
              參考案例
            </div>
          </div>
        </div>
        <div class="digital-flexbox12-item1">
          <div
            style="--src:url(../aa482bbfffb9d3dc894cad97465ab28b_violet.png)"
            class="digital-img3 digital-img3-posize"
          ></div>
        </div>
      </div>
      <!--說明欄位，但如果沒不出現，下方內容自動上移-->
      <div
        class="digital-flexbox13 digital-flexbox13-posize"
        v-html="exampleCases"
      ></div>
    </div>
    <div class="digital-part3-item">
      <div
        style="--src:url(../7b67ca8d18dd07b6be415dbc967e29c7_violet.png)"
        class="digital-group digital-group-posize7"
      >
        <!--第三部份 綜合建議-->
        <div
          class="digital-header-blue digital-header-posize"
          style="white-space: pre-line;text-align: left;"
          v-html="sectionThreeContent"
        ></div>
      </div>
    </div>
    <div v-if="suggestCheckbox">
      <div class="digital-part3-item">
        <div class="digital-desc3-box digital-desc3-box-posize">
          <div
            class="digital-desc3"
            style="white-space: pre-line;text-align: left;"
            v-html="suggestContent"
          ></div>
        </div>
      </div>
    </div>
    <div v-if="quadrantChartCheckbox">
      <div style="position:relative;">
        <img v-bind:src="quadrantImg" />
        <div class="digital-desc9" v-html="quadrantChartX"></div>
        <div class="digital-desc10" v-html="quadrantChartY"></div>
        <div class="digital-desc5" v-html="quadrantChartOne"></div>
        <div class="digital-desc6" v-html="quadrantChartTwo"></div>
        <div class="digital-desc7" v-html="quadrantChartThree"></div>
        <div class="digital-desc8" v-html="quadrantChartFour"></div>
      </div>
    </div>

    <!--數位轉型五大能力-->
    <div class="digital-rect3 digital-rect3-posize" v-if="imageCheckbox">
      <img v-if="image === null || image.trim() === ''" />
      <img v-else v-bind:src="image" />
    </div>
    <div class="digital-part3-item">
      <div class="digital-txt11-box digital-txt11-box-posize">
        <div
          class="digital-txt11"
          style="white-space: pre-line;text-align: left;"
          v-html="finalContent"
        ></div>
      </div>
    </div>
  </section>
</template>

<script>
import { use as echarts_use } from "echarts/core";
import { CanvasRenderer } from "echarts/renderers";
import { BarChart, RadarChart } from "echarts/charts";

import {
  GridComponent,
  TitleComponent,
  TooltipComponent,
  LegendComponent
} from "echarts/components";

import VChart, { THEME_KEY } from "vue-echarts";

echarts_use([
  CanvasRenderer,
  BarChart,
  RadarChart,
  GridComponent,
  TitleComponent,
  TooltipComponent,
  LegendComponent
]);

export default {
  components: { VChart },
  props: {
    sectionThreeCheckbox: Boolean,
    suggestCheckbox: Boolean,
    exampleCases: String,
    sectionThreeContent: String,
    suggestContent: String,
    quadrantChartCheckbox: Boolean,
    quadrantImg: String,
    quadrantChartX: String,
    quadrantChartY: String,
    quadrantChartOne: String,
    quadrantChartTwo: String,
    quadrantChartThree: String,
    quadrantChartFour: String,
    imageCheckbox: Boolean,
    image: String,
    finalContent: String,
  },
  data() {
    return {};
  },
  computed: {},
  created() {},
  methods: {},
  provide: {
    [THEME_KEY]: "ecTheme"
  }
};
</script>

<style></style>



