<template>
  <div id="all_3">
    <div class="title">
      <img src="../assets/titlenew.jpg" style="width: 100%;height: 35%"/>
    </div>
    <!-- /container -->
    <hr />

    <div id="all_2">
      <div class="card">
        <div class="card-header">
          <p class="myMOUSE" style="float: left;" @click="back_to_login"><</p>忘記密碼
        </div>
        <div class="card-body">
          <!-- 帳號 -->
          <p>請輸入註冊信箱，我們將會發送新密碼到信箱</p>
          <div class="form-group row">
            <label class="col-sm-2 col-form-label">帳號</label>
            <div class="col-sm-10">
              <input v-model="email" class="form-control" placeholder="請輸入註冊時的E-mail" />
            </div>
          </div>
          <!-- 密碼 -->
          <div align="center">
            <button class="btn btn-primary login_button" @click="login()">送出</button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import $ from "jquery";

export default {
  data() {
    return {
      email: ""
    };
  },
  created: function() {
    var _this = this;
    document.onkeydown = function(e) {
      let key = window.event.keyCode;
      if (key == 13) {
        _this.login();
      }
    };
  },
  methods: {
    back_to_login() {
      this.$router.push("/view/view_login");
    },
    login() {
      let username = this.email;
      if (username != "") {
        this.$swal({
          text: `確定要送出嗎?`,
          icon: "question",
          showCancelButton: true,
          confirmButtonText: "確定",
          cancelButtonText: "取消",
          preConfirm: () => {
            this.$swal({
              title: "郵件發送中",
              allowEscapeKey: false,
              allowOutsideClick: false,
              timer: 2500,
              onOpen: () => {
                this.$swal.showLoading();
                return this.axios
                  .post(
                    "/rise_api/public/index.php/email/password_forget",
                    {
                      email: this.email
                    }
                  )
                  .then(response => {
                    console.log(response);
                    if (response.status == 200) {
                      this.$swal({
                        icon: "success",
                        title: "請至信箱收取信件，並修改密碼",
                        showConfirmButton: false,
                        timer: 2500
                      });
                    }
                    //判斷有沒有此帳號
                    else if (response.status != 200) {
                      this.$swal({
                        icon: "error",
                        text: "無此帳號"
                      });
                    }
                    //避免有沒判斷到的，拋出錯誤(debug用) 理論上不會出現
                    else {
                      this.$swal({
                        icon: "error",
                        text: "資料有誤!"
                      });
                    }
                  });
              }
            });
          }
        });
      } else {
        this.$swal({
          icon: "error",
          text: "請輸入帳號"
        });
      }
    }
  }
};
</script>

<style>
#all_2 {
  width: 90%;
  margin: auto;
}
#all_3 {
  width: 70%;
  margin: auto;
}

.login_button {
  display: block;
  margin: 10px auto;
  width: 180px;
  color: #ffffff;
}

.myMOUSE:hover {
  cursor: pointer;
  color: #0056c2;
  text-decoration: underline;
}
</style>



