<template>
  <section class="answer-reference">
    <div class="row">
      <div
        style="--src: url(../f771f86d59f7596838773980fcb59488_violet.png)"
        class="digital-group digital-group-posize"
      >
        <div class="digital-header-posize reference-title">
          <span>第三部份 參考案例</span>
        </div>
      </div>
    </div>
    <div class="row">
      <div
        class="card-deck"
        v-if="isSmall()"
      >
        <div
          class="card"
          v-for="(reference, index) in references"
          :key="index"
        >
          <h3 class="card-title">{{ reference.dimension }}</h3>
          <img
            class="card-img-top ref-image"
            :src="reference.image"
            :alt="reference.title"
          />
          <h5 class="card-title">{{ reference.title }}</h5>
          <p class="card-text">
            <strong>痛點：</strong>
            <span v-html="reference.problem"></span>
          </p>
          <p class="card-text">
            <strong>解方：</strong>
            <span v-html="reference.solution"></span>
            <span> [<a :href="reference.url">更多內容</a>] </span>
          </p>
        </div>
      </div>
      <div
        class="reference-panel"
        v-else
      >
        <div
          class="ref-row"
          v-for="(reference, index) in references"
          :key="index"
        >
          <div class="ref-content-row">
            <div
              class="col-xl-2 col-lg-2 col-md-4 col-sm-4 text-center ref-dimension"
            >
              <span>{{ reference.dimension }}</span>
            </div>
            <div
              class="col-xl-10 col-lg-10 col-md-8 col-sm-8 text-left ref-title"
            >
              <span>{{ reference.title }}</span>
            </div>
          </div>
          <div class="ref-content-row">
            <div
              class="col-xl-9 col-lg-12 col-md-12 col-sm-12 ref-content-block"
            >
              <ul>
                <li class="ref-content text-left">
                  <strong>痛點：</strong>
                  <span v-html="reference.problem"></span>
                </li>
                <li class="ref-content text-left">
                  <strong>解方：</strong>
                  <span v-html="reference.solution"></span>
                  <span> [<a :href="reference.url">更多內容</a>] </span>
                </li>
              </ul>
            </div>
            <div
              class="col-xl-3 col-lg-12 col-md-12 col-sm-12 text-right ref-image-block"
            >
              <img
                class="ref-image"
                :src="reference.image"
                :alt="reference.title"
              />
            </div>
          </div>
        </div>
      </div>
    </div>
  </section>
</template>

<script>
import QrcodeVue from 'qrcode.vue';
import { isSmallScreen } from '../../util/window_utils';

export default {
  components: { QrcodeVue },
  props: {
    references: Array,
  },
  data() {
    return {};
  },
  computed: {},
  created() {},
  methods: {
    getDimension(dimensions) {
      return dimensions.join('、');
    },
    openUrl(url) {
      window.open(url, '_blank').focus();
    },
    isSmall() {
      return isSmallScreen();
    },
  },
};
</script>

<style scoped>
.answer-reference {
  padding-left: 10px;
  padding-right: 10px;
}

.reference-title {
  color: #ffffff;
  font-family: inherit;
  font-size: 2.3rem;
  font-weight: 700;
  padding-bottom: 10px;
  white-space: pre-line;
  text-align: left;
}

.reference-panel {
  padding-left: 50px;
  padding-right: 50px;
}

.ref-row {
  background-color: #f0f0f0;
  margin-bottom: 10px;
}

.ref-content-row {
  display: flex;
  flex-wrap: wrap;
}

.ref-dimension {
  background-color: black;
  color: white;
  font-size: 1.5rem;
  font-weight: 500;
}

.ref-title {
  font-size: 1.5rem;
  font-weight: 500;
  padding-left: 5px;
}

.ref-content-block {
  padding-left: 10px;
  padding-right: 20px;
}

.ref-content {
  font-size: 1.3rem;
  font-weight: 400;
  list-style-type: '●';
  padding-left: 5px;
}

.ref-content::marker {
  font-size: 1rem;
}

.ref-image-block {
  padding-left: 2px;
  padding-bottom: 10px;
  padding-right: 10px;
}

.ref-image-block > img {
  width: 260px;
  height: 150px;
  object-fit: cover;
}

.ref-image {
}
</style>



