// ref: https://stackoverflow.com/questions/40710628/how-to-convert-snake-case-to-camelcase-in-my-app
export const snakeToCamel = str =>
  str.toLowerCase().replace(/([-_][a-z])/g, group =>
    group
      .toUpperCase()
      .replace("-", "")
      .replace("_", "")
  );

// ref: https://dirask.com/posts/JavaScript-check-if-function-is-async-DNkKrp
export function isAsync(func) {
  if (func.constructor && func.constructor.name === "AsyncFunction") {
    return true;
  }
  return false;
}

// ref: https://medium.com/@trmaphi/lodash-isempty-value-you-might-be-using-it-the-wrong-way-d83210d7decf
export function isEmptyValue(value) {
  return (
    value === undefined ||
    value === null ||
    value === NaN ||
    (typeof value === "object" && Object.keys(value).length === 0) ||
    (typeof value === "string" && value.trim().length === 0)
  );
}



