<template>
  <section class="answer-analysis">
    <div class="row">
      <div
        style="--src: url(../f771f86d59f7596838773980fcb59488_violet.png)"
        class="digital-group digital-group-posize"
      >
        <div class="digital-header-posize analysis-title">
          <span>第二部份 競爭力表現細部解析</span>
        </div>
      </div>
    </div>
    <div class="row analysis-subtitle-pane">
      <div class="col-xl-12">
        <div class="digital-flexbox7 digital-flexbox7-posize">
          <div class="digital-flexbox7-item">
            <div
              style="
                --src: url(../459046be75e14b38db3dee6a1f7616cb_violet.png);
              "
              class="digital-group digital-group-posize1"
            >
              <div class="digital-title-posize analysis-subtitle">
                <span v-if="isSmall()">十項次構面表現</span>
                <span v-else>競爭力十項次構面表現</span>
              </div>
            </div>
          </div>
          <div class="digital-flexbox7-item1">
            <div
              style="
                --src: url(../f543f368b45c76491db3172401354589_violet.png);
              "
              class="digital-img1 digital-img1-posize"
            ></div>
          </div>
        </div>
      </div>
    </div>
    <div class="row">
      <div class="col-xl-12 analysis-panel">
        <div
          class="card-deck"
          v-if="isSmall()"
        >
          <div
            class="card"
            v-for="(dimension, index) in subDimensionScores"
            :key="index"
          >
            <h3
              class="card-title"
              v-if="index % 2 === 0"
            >
              {{ dimension.main }}
            </h3>
            <h5 class="card-title">{{ dimension.sub }}</h5>
            <p class="card-text">您的得分：{{ dimension.score }}</p>
            <p class="card-text">
              {{ careerName }}的平均分數：{{ dimension.careerAvg }}
            </p>
            <p class="card-text">
              {{ careerName }}中，您的 PR 值：{{ dimension.pr }}
            </p>
            <h5 :class="`card-title ${getResultClassByType(dimension.result)}`">
              評價：{{ dimension.result.text }}
            </h5>
          </div>
        </div>
        <table
          class="table analysis-table"
          v-else
        >
          <thead>
            <tr class="text-center analysis-table-header">
              <th scope="col">主構面</th>
              <th scope="col">次構面</th>
              <th scope="col">您的<br />得分</th>
              <th scope="col">{{ careerName }}<br />平均分數</th>
              <th scope="col">{{ careerName }}<br />您的 PR 值</th>
              <th scope="col">評價</th>
            </tr>
          </thead>
          <tbody>
            <tr
              class="text-center analysis-table-row"
              v-for="(dimension, index) in subDimensionScores"
              :key="index"
            >
              <td
                class="align-middle"
                :rowspan="getRowSpan(index)"
                v-if="getDisplay(index)"
              >
                {{ dimension.main }}
              </td>
              <td class="align-middle">{{ dimension.sub }}</td>
              <td class="align-middle">{{ dimension.score }}</td>
              <td class="align-middle">
                {{ dimension.careerAvg }}
              </td>
              <td class="align-middle">{{ dimension.pr }}</td>
              <td :class="getResultClassByType(dimension.result)">
                {{ dimension.result.text }}
              </td>
            </tr>
          </tbody>
        </table>
      </div>
      <div
        class="col-xl-12 text-left"
        style="padding-left: 50px;"
        v-if="isSmall()"
      >
        備註：在同業中 PR 值
	<!--
        <ul>
          <li
            v-for="(range, index) in prRange"
            :key="index"
          >
            {{ range.min }} ~ {{ range.max }}=<span
              :class="getResultClassByIndex(index)"
              >{{ range.text }}</span
            >
          </li>
        </ul>
	-->
	<ul>
		<li>≦25 為 <span class="result-bad">注意</span></li>
		<li>25&lt;PR≦75 為 <span class="result-mid">中等</span></li>
		<li>PR&gt;75 為 <span class="result-good">優勢</span></li>
	</ul>
      </div>
      <div
        class="col-xl-12 text-right"
        v-else
      >
        <div class="score-legend">
          備註：在同業中 PR 值
	  <!--
          <span
            v-for="(range, index) in prRange"
            :key="index"
            >{{ range.min }} ~ {{ range.max }}=<span
              :class="getResultClassByIndex(index)"
              >{{ range.text }}</span
            >
            <span v-if="index < prRange.length - 1">、</span></span
          >
	  -->
		  <span>≦25 為 <span class="result-bad">注意</span>、25&lt;PR≦75為<span class="result-mid">中等</span>、PR&gt;75為<span class="result-good">優勢</span></span>
	  
        </div>
      </div>
    </div>
    <div class="row align-middle sub-dimension-row">
      <div class="sub-dimension-panel">
        <div class="sub-dimension-title">次構面說明：</div>
        <div class="sub-dimension-box">
          <div class="card-deck" v-if="isSmall()">
            <div
              class="card"
              v-for="(desc, index) in subDimensionDescriptions"
              :key="index">
              <h3
                class="card-title"
                v-if="index % 2 === 0">{{ desc.mainName }}</h3>
                <h5 class="card-title">{{ desc.subName }}</h5>
                <p class="card-text">{{ desc.subDesc }}</p>
            </div>
          </div>
          <table class="sub-dimension-desc" v-else>
            <tbody>
              <tr
                class="sub-dimension-desc-row"
                v-for="(desc, index) in subDimensionDescriptions"
                :key="index"
              >
                <th
                  class="text-center main-dimension-desc-title"
                  v-if="index % 2 === 0"
                  rowspan="2"
                >
                  {{ desc.mainName }}
                </th>
                <th class="text-center sub-dimension-desc-title">
                  {{ desc.subName }}：
                </th>
                <td class="sub-dimension-desc-content">{{ desc.subDesc }}</td>
              </tr>
            </tbody>
          </table>
        </div>
      </div>
    </div>
  </section>
</template>

<script>
import _ from 'lodash';

import { isSmallScreen } from '../../util/window_utils';

const DESCRIPTION_ORDER = ['一', '二', '三', '四', '五'];

export default {
  components: {},
  props: {
    subDimensionScores: Array,
    dimensionDescriptions: Array,
    careerName: String,
    prRange: Array,
  },
  data() {
    return {
      subDimensionDescriptions: [],
    };
  },
  beforeMount() {},
  computed: {},
  created() {
    const subDimensionDescriptions = [];

    _.forEach(this.dimensionDescriptions, (mainItem) => {
      _.forEach(mainItem.sub, (sub) => {
        subDimensionDescriptions.push({
          mainName: mainItem.main.name,
          subName: sub.name,
          subDesc: sub.desc,
        });
      });
    });

    this.subDimensionDescriptions = subDimensionDescriptions;
  },
  methods: {
    getRowSpan(index) {
      if (index % 2 === 0) {
        return 2;
      } else {
        return 1;
      }
    },
    getDisplay(index) {
      return index % 2 === 0;
    },
    getResultClassByType(result) {
      return `result-${result.type}`;
    },
    getResultClassByIndex(index) {
      return `analysis-result-${index}`;
    },
    isSmall() {
      return isSmallScreen();
    },
  },
};
</script>

<style scoped>
.answer-analysis {
  padding-left: 10px;
  padding-right: 10px;
  margin-bottom: 10px;
}

.analysis-title {
  color: #ffffff;
  font-family: inherit;
  font-size: 2.3rem;
  font-weight: 700;
  padding-bottom: 5px;
  white-space: pre-line;
  text-align: left;
}

.analysis-subtitle-pane {
  padding-left: 30px;
  padding-right: 30px;
}

.analysis-subtitle {
  white-space: pre-line;
  text-align: left;
  color: #ffffff;
  font-family: inherit;
  font-size: 1.5rem;
  font-weight: 500;
}

.analysis-panel {
  padding-left: 50px;
  padding-right: 50px;
}

.analysis-table {
  width: 100%;
  height: 600px;
}

.analysis-table-header {
  font-size: 1.3rem;
}

.analysis-table-row {
  font-size: 1.5rem;
}

.analysis-result-0 {
  color: #ff0000;
}
.analysis-result-1 {
  color: #4472c4;
}
.analysis-result-2 {
  color: #70ad47;
}

.result-good {
  color: #70ad47;
}

.result-mid {
  color: #4472c4;
}

.result-bad {
  color: #ff0000;
}

.score-legend {
  margin-right: 50px;
  display: inline-block;
  background-color: #f0f0f0;
  color: #000000;

  font-family: inherit;
  font-size: 1rem;
  font-weight: 400;

  padding-left: 10px;
  padding-right: 10px;
}

.sub-dimension-row {
  margin-top: 0px;
}

.sub-dimension-panel {
  padding-left: 50px;
  padding-right: 50px;
  display: inline-block;
}

.sub-dimension-title {
  font-size: 1.8rem;
  font-weight: 500;
  color: #3a47a4;
  margin-bottom: 10px;
}

.sub-dimension-box {
  font-size: 1.3rem;
}

.sub-dimension-desc {
  border: 3px dashed #3a47a4;
}

/*
.sub-dimension-desc tr:nth-child(odd)  {
  border-bottom: 1px dashed #3a47a4;
}

*/
.sub-dimension-desc tr:nth-child(even) {
  border-bottom: 1px solid #3a47a4;
}

.sub-dimension-desc-row {
  height: 50px;
  margin-top: 5px;
  margin-bottom: 5px;
}

.main-dimension-desc-title {
  /* border-right: 2px dashed #3a47a4; */
  border-spacing: 0px 2rem;
  color: #4472c4;
  font-weight: 500;
  font-size: 1.3rem;
  width: 15%;
  padding-left: 10px;
  padding-right: 10px;
}

.sub-dimension-desc-title {
  color: #4472c4;
  font-weight: 500;
  font-size: 1.3rem;
  width: 15%;
  padding-left: 10px;
  padding-right: 10px;
}

.sub-dimension-desc-content {
  font-size: 1rem;
  padding-left: 10px;
  padding-right: 10px;
}
</style>



