<!--
	前台感謝頁
-->
<template>
  <div class="form-wrapper container">
    <div class="title">
      <img
        v-if="!appreciation_banner || appreciation_banner.trim() === ''"
        src="../assets/titlenew.jpg"
        style="width: 100%;height: 35%"
      />
      <img
        v-else
        v-bind:src="appreciation_banner"
        style="width: 100%;height: 35%"
      />
      <!--      <h1 v-if="!appreciation_banner || appreciation_banner.trim() === ''" style="position:absolute;top:175px;left: 650px;color: #4B0091;">{{name}}</h1>-->
    </div>

    <div>
      <!-- 總分的分數是總評的納入總分計算構面 不見得是下面的加總 -->
      <div
        align="center"
        style="background-color: #F2F2F2;height:100px;padding-top: 25px;white-space: pre-line;"
        valign="center"
        v-html="appreciation_content"
      ></div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      appreciation_content: "", //結尾文字
      appreciation_banner: "",
      name: ""
    };
  },
  created() {
    this.getFormList();
  },
  methods: {
    getFormList() {
      this.axios
        .get(
          // "/rise_api/public/index.php/view/getanswer/cbtk8sfg8dmf3eum/10663125@gm.nfu.edu.tw" //+

          "/rise_api/public/index.php/view/getanswer_1/" +
            this.$route.params.form_id +
            "/" +
            this.$route.params.username
        )
        .then(response => {
          let response_data = response.data[0];
          // //拿最下面的感謝文字
          console.log(response_data.result4.name);
          this.appreciation_content = response_data.result.appreciation_content;
          this.appreciation_banner = response_data.result.appreciation_banner;
          this.name = response_data.result4.name;
        });
    }
  }
};
</script>

<style>
#apprecitationContent {
  width: 100%;
  margin: auto;
}
.form-wrapper {
  padding: 0;
  width: 75%;
}
.form-question {
  padding: 20px;
  border-bottom: 2px dashed #bbb;
}
.send {
  margin: 10px 0;
  text-align: center;
}
.error .q-title {
  color: #f05050;
}

.font_color2 {
  color: #ffffff;
  background: #ffb54a;
  padding-bottom: 4px;
  font-size: 25px;
}
font {
  font-family: Microsoft JhengHei;
}
.strong_font {
  color: #1e7fc8;
  margin-left: -4px;
}
</style>



