<template>
  <section class="answer-title">
    <div class="row">
      <div class="col-xl-8 col-lg-6 col-md-12 col-sm-12 align-middle big-head">
        <!--總評標題文字-->
        <span
          class="title-content"
          style="white-space: pre-line; text-align: left"
          v-html="titleContent"
        ></span>
      </div>
      <div class="col-xl-4 col-lg-6 col-md-12 col-sm-12 align-middle download">
        <button class="btn btn-secondary download-button" v-if="!isSmall()" v-on:click="getPdf(companyName)">下載競爭力評量診斷結果</button>
      </div>
    </div>
    <div
      class="row"
      v-if="titleNoteContentCheckbox"
    >
      <span
        class="col-lg-12 align-middle title-note-content"
        style=""
        v-html="titleNoteContent"
      ></span>
    </div>
    <div class="row text-right">
      <span class="col-lg-12 align-bottom sponsor-content">
        經濟部商業司 生活服務業競爭力提升計畫
      </span>
    </div>
    <div
      style="--src: url(../339e121eb9091f91dca00043e2f55029.png)"
      class="digital-img digital-img-posize"
    ></div>
    <div
      class="row company-info"
      v-if="companyCheckbox"
    >
      <div class="col-xl-4 col-lg-4 col-md-3 col-sm-3 text-left">
        <div class="info-title">公司名稱</div>
        <div
          class="info-content"
          v-html="companyName"
        ></div>
      </div>
      <div class="col-xl-3 col-lg-3 col-md-3 col-sm-3 text-left">
        <div class="info-title">業別</div>
        <div
          class="info-content"
          v-html="careerName"
        ></div>
      </div>
      <div class="col-xl-2 col-lg-2 col-md-3 col-sm-3 text-left">
        <div class="info-title">公司統編</div>
        <div
          class="info-content"
          v-html="companyNum"
        ></div>
      </div>
      <div class="col-xl-3 col-lg-3 col-md-3 col-sm-3 text-left">
        <div class="info-title">檢測日期/時間</div>
        <div
          class="info-content"
          v-html="time"
        ></div>
      </div>
    </div>
  </section>
</template>

<script>
import { isSmallScreen } from '../../util/window_utils';

export default {
  props: {
    time: String,
    companyNum: String,
    companyName: String,
    companyCheckbox: Boolean,
    titleNoteContent: String,
    titleNoteContentCheckbox: Boolean,
    titleContent: String,
    pdfGenerator: Function,
    careerName: String,
  },
  data() {
    return {};
  },
  created() {},
  methods: {
    getPdf(companyName) {
      this.pdfGenerator(companyName);
    },
    isSmall() {
      return isSmallScreen();
    }
  },
};
</script>

<style scoped>
.answer-title {
  padding-left: 10px;
  padding-right: 10px;
  margin-bottom: 60px;
}
.big-head {
  padding-left: 50px;
}
.download {
  padding-right: 50px;
}

.download-button {
  font-size: 1.3rem;
  font-family: inherit;
}

.title-content{
  font-size: 3rem;
  font-weight: 700;
}
.title-note-content {
  white-space: pre-line;
  margin-top: 10px;
  margin-bottom: 10px;
  padding-left: 50px;
  padding-right: 50px;
  text-align: left;
  font-size: 1.3rem;
  
  font-family: inherit;
  font-weight: 300;
}

.sponsor-content{
  font-size: 1.3rem;
  font-family: inherit;
  font-weight: 500;
  padding-right: 50px;
  margin-bottom: 30px;
}

.company-info{
  font-family: inherit;
  padding-left: 50px;
  padding-right: 50px;
}
.info-title{
  font-family: inherit;
  font-size: 1.3rem;
  font-weight: 500;
}
.info-content{
  font-family: inherit;
  font-weight: 300;
  font-size: 1.3rem;
}
</style>



