<template>
  <div id="all_3">
    <div class="title">
      <img src="../assets/titlenew.jpg" style="width: 100%;height: 35%"/>
    </div>
    <!-- /container -->
    <hr />
    <div id="all_2">
      <div class="row">
        <div v-for="(val,index) in form" class="col-md-4 memo" style="margin-right:10px;">
          <table class="table">
            <thead align="center">
              <th class="h4_font" style="border:0px;" >
                <h4>{{val.form_name}}</h4>
              </th>
            </thead>
            <tbody>
              <td style="vertical-align:bottom;" valign="bottom">
                <p align="center">
                  <router-link
                    class="btn btn-info btn-lg btn_color"
                    :to="`./form/${val.form_id}`"
                    role="button"
                  >填寫</router-link>
                </p>
              </td>
            </tbody>
          </table>
          <!-- <h3>{{val.form_name}}</h3>
          <div class="align-items-end" >
            <p align="center">
              <router-link
                class="btn btn-info btn-lg btn_color"
                :to="`./form/${val.form_id}`"
                role="button"
              >填寫</router-link>
            </p>
          </div>-->
        </div>
      </div>

      <br />
      <div>
        <img src="../assets/index_about_rise.jpg" />
      </div>
      <div>
        <img src="../assets/index_contact_us.jpg" />
      </div>
    </div>
  </div>
</template>

<script>
import $ from "jquery";

export default {
  data() {
    return {
      form: []
    };
  },
  created() {
    console.log("/////");
    var chk=window.location.hash.includes("Guest");
    var chk2=window.location.hash.includes("Guest123");
    var chk3=this.$route.params.formId;
    if(chk==true && chk2==true && chk3!=undefined){
        // console.log(this.$route.params.formId);
        //console.log("sssss");      
        //console.log(chk3);  
       
    }
    this.getFormList();
  },
  methods: {
    
    getFormList() {
   
      this.axios
        .get("/rise_api/public/index.php/view/get_form")
        .then(response => {
          var form = this.form;
          $.each(response.data, function(key, value) {
            form.push({
              form_name: value.name,
              form_id: value.id
            });
          });
        });
    }
  }
};
</script>

<style>
#all_2 {
  width: 90%;
  margin: auto;
}
#all_3 {
  width: 70%;
  margin: auto;
}
.memo {
  background-color: rgba(255, 255, 255, 0.8);
  min-height: 160px;
  max-height: 250px;
  max-width: 30%;
  margin: 10px;
  padding: 0 1.5rem 0 1.5rem;
  border-radius: 0.8rem;
  box-shadow: 0px 0px 3px #ffffff;
  -moz-box-shadow: 0px 0px 3px #ffffff;
  -webkit-box-shadow: 0px 0px 3px #ffffff;
}

.write {
  font-size: 15px;
  background-color: #ff9e02;
}

h3 {
  text-align: center;
  font-weight: 900;
  color: #5b9bd1;
}

.h4_font {
  font-weight: 900;
  color: #5b9bd1;
  font-family:Microsoft JhengHei;
  margin: 0 auto;
  text-align: center;
  vertical-align: middle;
}
.btn_color {
  background: #5bc5c1;
  border: #5bc5c2;
}
</style>



