<template>
  <div id="all_3">
    <!-- /container -->
    <hr />
    <div id="all_2"></div>
  </div>
</template>

<script>
import $ from "jquery";

export default {
  data() {
    return {};
  },
  created() {
    this.register();
  },
  methods: {
    register() {
      let register_token = location.href.split("registered_check/");
      this.axios
        .put(
          "/rise_api/public/index.php/email/verification",
          {
            token: register_token[1]
          }
        )
        .then(response => {
          if (response.data == "-1") {
            this.$swal({
              icon: "error",
              title: "無此帳號，請聯絡信箱:mitech@iii.org.tw，謝謝。",
              showConfirmButton: false,
              timer: 2500
            }).then(() => {
              this.$router.push({
                name: "view_login"
                // params: { email: this.email }  //先註解掉 如果加入的話 可使跳轉到登入頁面時 帶入帳號(this.email要再改)
              });
            });
          } else if (response.data == "1") {
            this.$swal({
              icon: "success",
              title: "驗證成功",
              showConfirmButton: false,
              timer: 2500
            }).then(() => {
              this.$router.push({
                name: "view_login"
                // params: { email: this.email }  //先註解掉 如果加入的話 可使跳轉到登入頁面時 帶入帳號(this.email要再改)
              });
            });
          } else if (response.data == "0") {
            this.$swal({
              icon: "error",
              title: "此帳號已經驗證通過",
              showConfirmButton: false,
              timer: 2500
            }).then(() => {
              this.$router.push({
                name: "view_login"
                // params: { email: this.email }  //先註解掉 如果加入的話 可使跳轉到登入頁面時 帶入帳號(this.email要再改)
              });
            });
          }
          //理論上不會出現
          else {
            this.$swal({
              icon: "error",
              title: "驗證錯誤，請聯絡信箱:mitech@iii.org.tw，謝謝。",
              showConfirmButton: false,
              timer: 3000
            }).then(() => {
              this.$router.push({
                name: "view_home"
              });
            });
          }
        });
    }
  }
};
</script>

<style>
#all_2 {
  width: 90%;
  margin: auto;
}
#all_3 {
  width: 70%;
  margin: auto;
}
</style>



