<template>
  <div class="row answer-banner">
    <div class="col-lg-12">
      <img
        class="banner"
        v-if="!banner || banner.trim() === ''"
        src="../../assets/titlenew.jpg"
      />
      <img
        v-else
        src="./banner.png"
        class="banner"
      />
    </div>
  </div>
</template>

<script>
export default {
  props: {
    banner: String,
  },
  data() {
    return {};
  },
  computed: {},
  created() {},
  methods: {},
};
</script>

<style scopped>
.answer-banner {
  margin-bottom: 60px;
}
.banner {
  width: 100%;
  height: 100%;
}
</style>



