    floatAdd(arg1, arg2) {
      var r1, r2, m;
      try {
        r1 = arg1.toString().split(".")[1].length;
      } catch (e) {
        r1 = 0;
      }
      try {
        r2 = arg2.toString().split(".")[1].length;
      } catch (e) {
        r2 = 0;
      }
      m=Math.pow(10,Math.max(r1,r2))
      return Math.round((arg1*m+arg2*m))/m;
    },
    //比對總構面和有列入圖表的構面 有一樣的填入分數(供後面顯示使用)
    test2(source, target) {
      target.forEach((t) => {
        const index = source.dimensions.findIndex(
          (dimension) => dimension === t.subjectArr
        );
        if (index === -1) return;
        t.myscoreArr = source.score[index];
      });
    },
