<template>
  <section class="answer-summary">
    <div class="row">
      <div
        style="--src: url(../f771f86d59f7596838773980fcb59488_violet.png)"
        class="digital-group digital-group-posize"
      >
        <div class="col-xl-12 col-lg-12 align-middle part-1-title">
          <span>{{ sectionOneContent }}</span>
        </div>
      </div>
    </div>

    <div class="row">
      <div class="col-xl-6 col-lg-12 col-md-12 col-sm-12">
        <div class="summary-title">
          <div class="digital-flexbox7 digital-flexbox7-posize">
            <div class="digital-flexbox7-item">
              <div
                style="
                  --src: url(../459046be75e14b38db3dee6a1f7616cb_violet.png);
                "
                class="digital-group digital-group-posize1 part-1-subtitle-pane"
              >
                <div class="digital-title-posize part-1-subtitle">
                  <span>{{ totalScoreTitleContent }}</span>
                </div>
              </div>
            </div>
            <div class="digital-flexbox7-item1">
              <div
                style="
                  --src: url(../f543f368b45c76491db3172401354589_violet.png);
                "
                class="digital-img1 digital-img1-posize"
              ></div>
            </div>
          </div>
        </div>
        <div class="summary-content">
          <div class="total-label">
            <span>總分：</span>
            <span class="total-count">{{ totalCount }}</span>

            <span>/</span>
            <span v-html="maxScore"></span>
            <span>分</span>
          </div>
          <div>
            <v-chart
              class="summary-chart-canvas"
              :option="summaryOption"
            />
          </div>
        </div>
      </div>
      <div class="col-xl-6 col-lg-12 col-md-12 col-sm-12">
        <div class="level-title">
          <div class="digital-flexbox10 digital-flexbox10-posize">
            <div class="digital-flexbox10-item">
              <div
                style="
                  --src: url(../808b869aa7dd79b406df995fd65df9ff_violet.png);
                "
                class="digital-group digital-group-posize2 part-1-subtitle-pane"
              >
                <div class="digital-title-posize1 part-1-subtitle">
                  <span>{{ distributionContent }}</span>
                </div>
              </div>
            </div>
            <div class="digital-flexbox10-item1">
              <div
                style="
                  --src: url(../a6076b69ec1cf67c916b2febb32d731c_violet.png);
                "
                class="digital-img2 digital-img2-posize"
              ></div>
            </div>
          </div>
        </div>
        <div
          class="card-deck"
          v-if="isSmall()"
        >
          <div class="card">
            <div class="card-body">
              <h5 class="card-title">
                <span class="level-value">{{ levelContent.myLabel }}</span>
                <span>/ Level {{ levelContent.maxLevel }}</span>
              </h5>
              <p class="card-text">
                <span class="pr-title">在生活服務業中</span><br />
                <span class="pr-intro">PR值 </span>
                <span class="pr-value">{{ industryPr }}</span>
              </p>
              <p class="card-text">
                <span class="pr-title">在{{ careerName }}中</span><br />
                <span class="pr-intro">PR值 </span>
                <span class="pr-value">{{ careerPr }}</span>
              </p>
            </div>
          </div>
        </div>

        <div
          class="level-content"
          v-else
        >
          <div class="level-label">
            <span class="level-value">{{ levelContent.myLabel }}</span
            ><span>/ Level {{ levelContent.maxLevel }}</span>
          </div>
          <div>
            <div class="pr-industry-label">
              <span class="pr-title">在生活服務業中</span><br />
              <span class="pr-intro">PR值 </span>
              <span class="pr-value">{{ industryPr }}</span>
            </div>
            <div class="pr-career-label">
              <span class="pr-title">在{{ careerName }}中</span><br />
              <span class="pr-intro">PR值 </span>
              <span class="pr-value">{{ careerPr }}</span>
            </div>
          </div>
          <div>
            <p class="pr-desc">{{ levelContent.myDesc }}</p>
          </div>
        </div>
      </div>
    </div>
    <div class="row radar-head">
      <div class="digital-flexbox12-item">
        <div
          style="
            --src: url(../f4d1a35375a159400b4b6f1ae37af4c4_violet.png);
          "
          class="digital-group digital-group-posize3 part-1-radartitle-pane"
        >
          <div class="digital-title-posize2 part-1-radartitle">
            <span>{{ capabilityContent }}</span>
          </div>
        </div>
      </div>
      <div class="digital-flexbox12-item1">
        <div
          style="
            --src: url(../aa482bbfffb9d3dc894cad97465ab28b_violet.png);
          "
          class="digital-img3 digital-img3-posize"
        ></div>
      </div>
    </div>
    <div class="row radar-row">
      <div class="col-xl-6 col-lg-12 col-md-12 col-sm-12 radar-panel">
        <div class="radar-title">
          <span>比較{{ wording.industry }}平均得分</span>
        </div>

        <div class="radar-chart">
          <v-chart
            class="radar-chart-canvas"
            :option="industryOption"
          />
        </div>
        <div class="radar-desc">
          <ul>
            <li
              v-for="chartWording in industryChartWordings"
              :key="chartWording.dimension"
            >
              <span>
                <strong class="radar-desc-primary">{{
                  chartWording.dimension
                }}</strong
                >為&nbsp;
                <strong class="radar-desc-score"
                  >{{ chartWording.myScore }} 分</strong
                >
                ，{{ wording.industry }}平均&nbsp;
                <strong class="radar-desc-score"
                  >{{ chartWording.targetScore }} 分</strong
                >，得分
                <span
                  v-if="
                    chartWording.myScore.toString() ===
                    chartWording.targetScore.toString()
                  "
                  class="radar-desc-equal"
                  >{{ wording.equal }}</span
                >
                <span
                  v-if="chartWording.myScore > chartWording.targetScore"
                  class="radar-desc-higher"
                  >{{ wording.higher }}</span
                >
                <span
                  v-else-if="chartWording.myScore < chartWording.targetScore"
                  class="radar-desc-lower"
                  >{{ wording.lower }}</span
                >
              </span>
            </li>
          </ul>
        </div>
      </div>
      <div class="col-xl-6 col-lg-12 col-md-12 col-sm-12 radar-panel">
        <div class="radar-title">
          <span>在{{ wording.industry }}中您的 PR 值</span>
        </div>
        <div class="radar-chart">
          <v-chart
            class="radar-chart-canvas"
            :option="industryPrOption"
          />
        </div>
        <div class="radar-desc">
          <ul>
            <li
              v-for="chartWording in industryPrWording"
              :key="chartWording.dimension"
            >
              <span>
                <strong class="radar-desc-primary">{{
                  chartWording.dimension
                }}</strong
                >，在{{ wording.industry }}中的 PR 值為
                <strong class="radar-desc-primary">{{
                  chartWording.prValue
                }}</strong>
              </span>
            </li>
          </ul>
        </div>
      </div>
    </div>
    <div class="row radar-row">
      <div class="col-xl-6 col-lg-12 col-md-12 col-sm-12 radar-panel">
        <div class="radar-title">
          <strong style="color: #00816d; font-size: 22px">
            比較{{ careerName }}平均得分
          </strong>
        </div>
        <div class="radar-chart">
          <v-chart
            class="radar-chart-canvas"
            :option="careerOption"
          />
        </div>
        <div class="radar-desc">
          <ul>
            <li
              v-for="chartWording in careerChartWordings"
              :key="chartWording.dimension"
            >
              <span>
                <strong class="radar-desc-primary">{{
                  chartWording.dimension
                }}</strong
                >為&nbsp;
                <strong class="radar-desc-score"
                  >{{ chartWording.myScore }}分</strong
                >
                ，{{ careerName }}平均&nbsp;
                <strong class="radar-desc-score"
                  >{{ chartWording.targetScore }} 分</strong
                >，得分
                <span
                  v-if="
                    chartWording.myScore.toString() ===
                    chartWording.targetScore.toString()
                  "
                  class="radar-desc-equal"
                  >{{ wording.equal }}</span
                >
                <span
                  v-if="chartWording.myScore > chartWording.targetScore"
                  class="radar-desc-higher"
                  >{{ wording.higher }}</span
                >
                <span
                  v-else-if="chartWording.myScore < chartWording.targetScore"
                  class="radar-desc-lower"
                  >{{ wording.lower }}</span
                >
              </span>
            </li>
          </ul>
        </div>
      </div>
      <div class="col-xl-6 col-lg-12 col-md-12 col-sm-12 radar-panel">
        <div class="radar-title">
          <strong style="color: #00816d; font-size: 22px"
            >在{{ careerName }}中您的 PR 值</strong
          >
        </div>
        <div class="radar-chart">
          <v-chart
            class="radar-chart-canvas"
            :option="careerPrOption"
          />
        </div>
        <div class="radar-desc">
          <ul>
            <li
              v-for="chartWording in careerPrWording"
              :key="chartWording.dimension"
            >
              <span style="font-size: 14px">
                <strong class="radar-desc-primary">{{
                  chartWording.dimension
                }}</strong
                >，在{{ careerName }}中的 PR 值為
                <strong class="radar-desc-primary">{{
                  chartWording.prValue
                }}</strong>
              </span>
            </li>
          </ul>
        </div>
      </div>
    </div>

    <div class="row align-middle dimension-row">
      <div class="col-xl-12 col-lg-12 col-md-12 col-sm-12 dimension-panel">
        <div class="dimension-title">主構面說明：</div>

        <div
          class="card-deck"
          v-if="isSmall()"
        >
          <div
            class="card"
            v-for="dimIntro in dimensionIntroductions"
            :key="dimIntro.name"
          >
            <div class="card-body">
              <h4 class="card-title">{{ dimIntro.name }}：</h4>
              <p class="card-text">{{ dimIntro.intro }}</p>
            </div>
          </div>
        </div>
        <table
          class="table dimension-desc"
          v-else
        >
          <tbody>
            <tr
              class="dimension-desc-row"
              v-for="dimIntro in dimensionIntroductions"
              :key="dimIntro.name"
            >
              <th class="dimension-desc-title">{{ dimIntro.name }}：</th>
              <td class="dimension-desc-content">
                {{ dimIntro.intro }}
              </td>
            </tr>
          </tbody>
        </table>
      </div>
    </div>
  </section>
</template>

<script>
import { use as echarts_use } from 'echarts/core';
import { CanvasRenderer } from 'echarts/renderers';
import { BarChart, RadarChart } from 'echarts/charts';
import {
  GridComponent,
  LegendComponent,
  TitleComponent,
  TooltipComponent,
} from 'echarts/components';
import VChart, { THEME_KEY } from 'vue-echarts';
import { isSmallScreen } from '../../util/window_utils';

echarts_use([
  CanvasRenderer,
  BarChart,
  RadarChart,
  GridComponent,
  TitleComponent,
  TooltipComponent,
  LegendComponent,
]);

export default {
  components: { VChart },
  props: {
    sectionOneContent: String,
    totalScoreTitleContent: String,
    totalCount: Number,
    maxScore: Number,
    summaryOption: Object,
    distributionContent: String,
    capabilityContent: String,
    chartOptions: Array,
    wording: Object,
    industryChartWordings: Array,
    industryOption: Object,
    industryPrOption: Object,
    careerChartWordings: Array,
    careerOption: Object,
    careerPrOption: Object,
    careerName: String,
    chartMaxScore: Number,
    industryPr: Number,
    careerPr: Number,
    levelContent: Object,
    industryPrWording: Array,
    careerPrWording: Array,
    dimensionIntroductions: Array,
  },
  data() {
    return {};
  },
  computed: {},
  created() {},
  methods: {
    isSmall() {
      return isSmallScreen();
    },
  },
  provide: {
    [THEME_KEY]: 'ecTheme',
  },
};
</script>

<style scoped>
.answer-summary {
  padding-left: 10px;
  padding-right: 10px;
  margin-bottom: 10px;
}

.part-1-title {
  color: #ffffff;
  font-family: inherit;
  font-size: 2.3rem;
  font-weight: 700;
  padding-left: 5px;
  padding-top: 5px;
  padding-bottom: 5px;
  white-space: pre-line;
  text-align: left;
}

.part-1-subtitle-pane {
  padding-top: 0px;
  padding-bottom: 10px;
}

.part-1-subtitle-pane {
  padding-top: 0px;
  padding-bottom: 60px;
}
.part-1-radartitle-pane {
  padding-top: 0px;
  padding-bottom: 10px;
}

.part-1-subtitle {
  white-space: pre-line;
  text-align: left;
  color: #ffffff;
  font-family: inherit;
  font-size: 1.5rem;
  font-weight: 500;
}
.part-1-radartitle {
  white-space: pre-line;
  text-align: left;
  color: #ffffff;
  font-family: inherit;
  font-size: 1.5rem;
  font-weight: 500;
}

.summary-title {
  padding-left: 30px;
}
.summary-content {
  font-family: inherit;
  padding-left: 30px;
}
.total-label {
  font-family: inherit;
  font-size: 1.5rem;
  font-weight: 400;
}

.total-count {
  color: #3a47a4;
  font-size: 2.3rem;
  font-weight: 700;
}

.summary-chart-canvas {
  height: 350px;
  width: 500px;
}

.level-title {
  padding-right: 30px;
  padding-left: 50px;
}

.level-content {
  padding-right: 30px;
  padding-left: 50px;
  font-family: inherit;
}

.level-label {
  font-family: inherit;
  font-size: 1.8rem;
}

.level-value {
  font-family: inherit;
  font-size: 2.3rem;
  font-weight: 700;
  color: #3a47a4;
}

.pr-industry-label {
  width: 38%;
  float: left;
  flex: auto;
  font-family: inherit;
  font-size: 1.8rem;
  margin-top: 60px;
}

.pr-career-label {
  width: 62%;
  float: left;
  flex: auto;
  font-family: inherit;
  font-size: 1.8rem;
  margin-top: 60px;
}

.pr-title {
  font-size: 1.3rem;
  font-family: inherit;
}

.pr-intro {
  font-size: 1.8rem;
  font-weight: 500;
  color: #3a47a4;
}

.pr-value {
  font-size: 2.3rem;
  font-weight: 700;
  color: #3a47a4;
}

.pr-desc {
  font-size: 1.3rem;
  font-weight: 400;
  display: inline-block;
  margin-top: 45px;
  padding-left: 6px;
  padding-right: 6px;
  color: #000000;
  background-color: #f0f0f0;
}

.radar-head {
  padding-left: 50px;
  padding-right: 50px;
  margin-top: 30px;
  margin-bottom: 20px;
}

.radar-row {
  padding-left: 50px;
  padding-right: 50px;
  margin-bottom: 10px;
}

.radar-panel {
  font-family: inherit;
  font-weight: 400;
}

.radar-title {
  margin-bottom: 10px;
  color: #00816d;
  font-size: 1.5rem;
  font-weight: 700;
}

.radar-chart {
  margin-top: 5px;
  width: 99%;
}

.radar-desc {
  font-family: inherit;
  font-size: 0.8rem;
  font-weight: 300;
}

.radar-desc > strong {
  font-weight: 500;
}

.radar-desc-primary {
  color: #1e7fc8;
  font-weight: 500;
}

.radar-desc-score {
  font-weight: 500;
}

.radar-desc-lower {
  font-weight: 500;
  color: #ff0000;
  margin-left: -4px;
}

.radar-desc-higher {
  font-weight: 500;
  color: #70ad47;
  margin-left: -4px;
}

.radar-desc-equal {
  font-weight: 500;
  color: #000000;
  margin-left: -4px;
}

.radar-chart-canvas {
  height: 350px;
  width: 500px;
}

.dimension-row {
  margin-top: 0px;
}

.dimension-panel {
  padding-left: 50px;
  padding-right: 50px;
  display: inline-block;
}

.dimension-title {
  font-size: 1.8rem;
  font-weight: 500;
  color: #3a47a4;
  margin-bottom: 10px;
}

.dimension-desc {
  border: 3px dashed #3a47a4;
}

.dimension-desc-row {
  height: 50px;
}

.dimension-desc-title {
  color: #4472c4;
  font-weight: 500;
  font-size: 1.3rem;
  width: 15%;
  padding-left: 10px;
  padding-right: 10px;
}

.dimension-desc-content {
  font-size: 1.3rem;
  padding-left: 10px;
  padding-right: 10px;
}

@media only screen and (max-width: 600px) {
  .summary-content {
    font-family: inherit;
    padding-left: 5px;
  }
  .summary-chart-canvas {
    height: 450px;
    width: 250px;
  }

  .radar-chart-canvas {
    height: 400px;
    width: 260px;
  }
  .radar-row {
    padding-left: 5px;
    padding-right: 10px;
    margin-bottom: 10px;
  }
}
</style>



