<template>
  <div id="all_3">
    <div class="title">
      <img src="../assets/titlenew.jpg" style="width: 100%;height: 35%"/>
    </div>
    <!-- /container -->
    <hr />

    <div id="all_2">
      <div class="card">
        <div class="card-header">登入</div>
        <div class="card-body">
          <!-- 帳號 -->
          <div class="form-group row">
            <label class="col-sm-2 col-form-label">帳號</label>
            <div class="col-sm-10">
              <input v-model="email" class="form-control" placeholder="請輸入註冊時的E-mail" />
            </div>
          </div>
          <!-- 密碼 -->
          <div class="form-group row">
            <label class="col-sm-2 col-form-label">密碼</label>
            <div class="col-sm-10">
              <input v-model="password" type="password" class="form-control" placeholder="Password" />
            </div>
          </div>
          <div align="center">
            <div class="row">
            <button class="btn btn-secondary login_button" @click="guest_login()">以訪客身分登入</button>
            <button class="btn btn-primary login_button" @click="login()">登入</button></div>
          </div>
          <div align="left">
            <router-link class="router_p" style="float: left;" :to="`./view_password_forget`">忘記密碼</router-link>
            <router-link class="router_p" style="float: right;" :to="`./view_register`">還不是會員？</router-link>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import $ from "jquery";

export default {
  data() {
    return {
      email: "",
      // email: this.$route.params.userId,
      password: ""
    };
  },
  created: function() {
    //如果是從註冊頁面近來的話  自動填入帳號
    this.email = this.$route.params.userId;
    var _this = this;
    document.onkeydown = function(e) {
      let key = window.event.keyCode;
      if (key == 13) {
        _this.login();
      }
    };
  },
  methods: {
    login() {
      let username = this.email;
      let password = this.password;

      if (username != "" && password != "") {
        localStorage.setItem('pa', password);
        this.$store
          .dispatch("view_login", { username, password })
          .then(Response => {
            if (Response.data[0].suc == "T") {
              this.$swal({
                icon: "success",
                title: "登入成功",
                showConfirmButton: false,
                timer: 1500
              }).then(() => {
                this.$router.push("/view/view_home");
                this.$router.go();
              });
            } else {
              this.$swal({
                icon: "error",
                title: "帳號或密碼錯誤",
                showConfirmButton: false,
                timer: 1500
              });
            }
          });
      } else {
        this.$swal({
          icon: "error",
          text: "請輸入帳號及密碼!"
        });
      }
    },
    guest_login(){
      let username = "Guest";
      let password = "Guest123";
      this.$store
          .dispatch("view_login", { username, password })
          .then(Response => {
            if (Response.data[0].suc == "T") {
              this.$swal({
                icon: "success",
                title: "登入成功",
                showConfirmButton: false,
                timer: 1500
              }).then(() => {
                this.$router.push("/view/view_home");
                this.$router.go();
              });
            } else {
              this.$swal({
                icon: "error",
                title: "帳號或密碼錯誤",
                showConfirmButton: false,
                timer: 1500
              });
            }
          });
    }
  }
};
</script>

<style>
#all_2 {
  width: 90%;
  margin: auto;
}
#all_3 {
  width: 70%;
  margin: auto;
}
.form-signin input[type="email"] {
  /*  border-bottom-right-radius: 0;
  border-bottom-left-radius: 0;*/
  text-indent: 35px;
  background-size: 30px 35px;
  background-image: url("../assets/email.png");
  background-position: 7px 4px;
  background-repeat: no-repeat;
}
.form-signin input[type="password"] {
  /*  border-top-left-radius: 0;
  border-top-right-radius: 0;*/
  text-indent: 35px;
  background-size: 25px 25px;
  background-image: url("../assets/password.png");
  background-position: 9px 9px;
  background-repeat: no-repeat;
}
.bbbox {
  border: 1px solid #cccccc;
  width: auto;
  height: 200px;
  background-color: #ffffff;
  padding: 5px;
  overflow: auto;
}
.button_big {
  width: 100px;
  margin-bottom: 10px;
}
.router_p {
  text-decoration: underline;
  cursor: pointer;
}
.login_button {
  display: block;
  margin: 10px auto;
  width: 180px;
  color: #ffffff;
}
</style>



