import Vue from 'vue'
import Vuex from 'vuex'
import axios from 'axios'
import { ready } from 'jquery'

Vue.use(Vuex)

export default new Vuex.Store({
	state: {
		status: '',
		view_status: '',
		token: localStorage.getItem('token') || '',
		user: {}
	},
	mutations: {
		auth_request(state) {
			state.status = 'loading'
		},
		auth_success(state, token, user) {
			state.status = 'success'
			state.token = token
			state.user = user
		},
		view_auth_success(view_status, token, user) {
			view_status.status = 'success'
			view_status.token = token
			view_status.user = user
		},
		auth_error(state) {
			state.status = 'error'
		},
		logout(state) {
			state.status = ''
			state.token = ''
		},
		view_logout(view_status) {
			view_status.status = ''
			view_status.token = ''
		},
	},
	actions: {
		login({ commit }, user) {
			return new Promise((resolve, reject) => {
				commit('auth_request')
				const user2 = user;
				axios.post("/rise_api/public/index.php/login/token", {
					username: user.email,
					password: user.password
				}).then(resp => {
					const token = resp.data["0"].token
					const authority = resp.data["0"].authority
					axios.put("/rise_api/public/index.php/login/update_token", {
						token: token,
						username: user2.email
					}).then(() => {
						localStorage.setItem('token', token)
						localStorage.setItem('authority', authority)
						localStorage.setItem('time', parseInt(new Date().getTime() / 1000))
						localStorage.setItem('username', user2.email)
						// Add the following line:
						axios.defaults.headers.common['Authorization'] = token
						const a = user2.email;
						commit('auth_success', token, a)
						resolve(resp)
					})
				})
					.catch(err => {
						commit('auth_error')
						localStorage.removeItem('token')
						reject(err)
					})
			})
		},
		view_login({ commit }, user) {
			return new Promise((resolve, reject) => {
				commit('auth_request')
				axios.post("/rise_api/public/index.php/view/login_token", {
					username: user.username,
					password: user.password
				}).then(resp => {
					// if (resp.data[0].suc == "T") {
						const token = resp.data["0"].token
						const uname = resp.data["0"].name
            const cname = resp.data["0"].cname
            const cnum = resp.data["0"].cnum
						axios.put("/rise_api/public/index.php/view/update_token", {
							token: token,
							username: user.username
						}).then(() => {
							localStorage.setItem('view_token', token)
							localStorage.setItem('view_time', parseInt(new Date().getTime() / 1000))
							localStorage.setItem('view_username', user.username)
							localStorage.setItem('uname',uname)
              localStorage.setItem('cname',cname)
              localStorage.setItem('cnum',cnum)

							commit('view_auth_success', token, user.username)
							resolve(resp)

						})
					// }
				})
					.catch(err => {
						commit('auth_error')
						localStorage.removeItem('token')
						reject(err)
					})
			})
		},
		// register({commit}, user){
		// 	return new Promise((resolve, reject) => {
		//         commit('auth_request')
		//         axios({url: 'http://localhost:3000/register', data: user, method: 'POST' })
		//         .then(resp => {
		//             const token = resp.data.token
		//             const user = resp.data.user
		//             localStorage.setItem('token', token)
		//             // Add the following line:
		//             axios.defaults.headers.common['Authorization'] = token
		//             commit('auth_success', token, user)
		//             resolve(resp)
		//         })
		//         .catch(err => {
		//             commit('auth_error', err)
		//             localStorage.removeItem('token')
		//             reject(err)
		//         })
		//     })
		// },
		logout({ commit }) {
			return new Promise((resolve, reject) => {
				commit('logout')
				localStorage.removeItem('token')
				localStorage.removeItem('authority')
				localStorage.removeItem('username')
				delete axios.defaults.headers.common['Authorization']
				resolve()
			})
		},
		view_logout({ commit }) {
			return new Promise((resolve, reject) => {
				commit('view_logout')
				localStorage.removeItem('view_token')
				localStorage.removeItem('view_status')
				localStorage.removeItem('view_time')
				localStorage.removeItem('view_username')
        localStorage.removeItem('customer')
        localStorage.setItem('sso', 'true')
        localStorage.removeItem('cname')
        localStorage.removeItem('cnum')
				delete axios.defaults.headers.common['Authorization']
				resolve()
			})
		}
	},
	getters: {
		isLoggedIn: state => !!state.token,
		authStatus: state => state.status,
		view_isLoggedIn: view_state => !!view_state.token,
	}
})



