<template>
  <div id="all_3">
    <div class="title">
      <img src="../assets/titlenew.jpg" style="width: 100%;height: 35%"/>
    </div>

    <!-- /container -->
    <hr />

    <div id="all_2">
      <div class="card">
        <form id="form1" class="form-signin" @submit="register"><!--@submit="register"-->
          <div class="card-header">
            <p class="myMOUSE" style="float: left;" @click="back_to_login"></p>註冊
          </div>
          <div class="card-body">
            <!-- 帳號 -->
            <div class="form-group row">
              <label class="col-sm-2 col-form-label">帳號</label>
              <div class="col-sm-10">
                <input
                  type="email"
                  v-model="username"
                  pattern="[a-z0-9._%+-]+@[a-z0-9.-]+\.[a-z]{2,}$"
                  class="form-control"
                  placeholder="帳號"
                  required
                  autofocus
                />
              </div>
            </div>
            <br />
       
            <div class="form-group row">
              <label class="col-sm-2 col-form-label">聯絡信箱</label>
              <div class="col-sm-10">
                <input
                  type="email"
                  v-model="email"
                  pattern="[a-z0-9._%+-]+@[a-z0-9.-]+\.[a-z]{2,}$"
                  class="form-control"
                  placeholder="電子信箱"
                  required
                  autofocus
                />
              </div>
            </div>
            <br />
            <div class="form-group row">
              <label class="col-sm-2 col-form-label">密碼</label>
              <div class="col-sm-10">
                <input
                  type=""
                  v-model="password"
                  pattern="^(?=.*\d)(?=.*[a-z])(?=.*[A-Z]).{8,}$"
                  class="form-control"
                  placeholder="密碼"
                  title="密碼規則至少8碼且有一個數字、小寫英文及大寫英文"
                  required
                />
              </div>
            </div>
            <br />

            <div class="form-group row">
              <label class="col-sm-2 col-form-label">再次輸入密碼</label>
              <div class="col-sm-10">
                <input
                  type=""
                  v-model="password2"
                  pattern="^(?=.*\d)(?=.*[a-z])(?=.*[A-Z]).{8,}$"
                  class="form-control"
                  placeholder="密碼"
                  title="密碼規則至少8碼且有一個數字、小寫英文及大寫英文"
                  required
                />
              </div>
            </div>
            <br />

            <div class="form-group row">
              <label class="col-sm-2 col-form-label">會員名稱</label>
              <div class="col-sm-10">
                <input
                  type="text"
                  v-model="m_name"
                  class="form-control"
                  placeholder="會員名稱"
                  required
                />
              </div>
            </div>
            <br />

            <div class="form-group row">
              <label class="col-sm-2 col-form-label">公司名稱</label>
              <div class="col-sm-10">
                <input
                  type="text"
                  v-model="c_name"
                  class="form-control"
                  placeholder="公司名稱"
                  required
                />
              </div>
            </div>
            <br />

            <div class="form-group row">
              <label class="col-sm-2 col-form-label">統一編號</label>
              <div class="col-sm-10">
                <input
                  type="text"
                  v-model="c_num"
                  class="form-control"
                  pattern="\[0-9]{8}"
                  placeholder="統一編號"
                  title="請輸入統一編號"
                  required
                />
              </div>
            </div>
            <br />

            <div class="form-group row">
              <label class="col-sm-2 col-form-label">聯絡電話</label>
              <div class="col-sm-10">
                <input type="tel" v-model="phone" class="form-control" placeholder="連絡電話" required />
              </div>
            </div>
            <br />

            <div class="form-group row">
              <label class="col-sm-2 col-form-label">員工人數</label>
              <div class="col-sm-10">
                <select class="browser-default custom-select" v-model="workers" required>
                  <!-- <option value disabled selected>請選擇員工人數</option> -->
                  <option v-for="(item, index) in forms2" :value="item">({{index + 1}}){{item}}</option>
                </select>
              </div>
            </div>
            <br />

            <div class="form-group row">
              <label class="col-sm-2 col-form-label">產業類別</label>
              <div class="col-sm-10">
                <select class="browser-default custom-select" v-model="industry" required>
                  <option v-for="(item, index) in forms" :value="item">({{index + 1}}){{item}}</option>
                </select>
              </div>
            </div>
            <br />

            <div class="form-group row">
              <label class="col-sm-2 col-form-label">個人資料提供同意書</label>
              <div class="col-sm-10">
                <div class="bbbox">
                  <p align="left">
                    <b>財團法人資訊工業策進會(下稱本會)</b>為遵守個人資料保護法令及本會個人資料保護政策、規章，於向您蒐集個人資料前，依法向您告知下列事項，敬請詳閱。
                    <br />
                    <br />一、蒐集目的及類別
                    本會因辦理或執行業務、活動、計畫、調查、診斷..等，相關服務及供本會用於內部行政管理、陳報主管機關或其他合於本會捐助章程所定業務、寄送本會或產業相關活動訊息之蒐集目的，而需獲取您下列個人資料類別：姓名、聯絡方式(如電話號碼、職稱、電子信箱、居住或工作地址等)、統一編號，或其他得以直接或間接識別您個人之資料。
                    ※您日後如不願再收到本會所寄送之任何訊息，可於收到前述訊息時，直接點選訊息內拒絕接受之連結。
                    <br />
                    <br />二、個人資料利用之期間、地區、對象及方式
                    除涉及國際業務或活動外，您的個人資料僅供本會於中華民國領域、在前述蒐集目的之必要範圍內，以合理方式利用至蒐集目的消失為止。
                    <br />
                    <br />三、當事人權利
                    您可依前述業務、活動所定規則或依本會網站（https://www.iii.org.tw/）「個資當事人行使權利專頁」公告方式向本會行使下列權利：
                    (一) 查詢或請求閱覽。
                    <br />(二) 請求製給複製本。
                    <br />(三) 請求補充或更正。
                    <br />(四) 請求停止蒐集、處理及利用
                    <br />(五) 請求刪除您的個人資料。
                    <br />
                    <br />四、不提供個人資料之權益影響
                    若您未提供正確或不提供個人資料，本會將無法為您提供蒐集目的之相關服務。
                    <br />
                    <br />五、您瞭解此一同意書符合個人資料保護法及相關法規之要求，且同意本會留存此同意書，供日後取出查驗。
                  </p>
                  <h2>
                    <b>
                      <p align="left">個人資料之同意提供：</p>
                    </b>
                  </h2>
                  <p align="left">
                    一、本人已充分獲知且已瞭解上述貴會告知事項。
                    <br />
                  </p>
                  <p align="left">
                    二、本人同意貴會於所列蒐集目的之必要範圍內，蒐集、處理及利用本人之個人資料。
                    <br />
                  </p>
                </div>
              </div>
            </div>
            <br />

            <div align="center">
              <input type="checkbox" required="required" />我同意
              <br />
              <br />
              <button class="btn btn-primary" @click.prevent='register()' >註冊</button>
            </div>
          </div>
        </form>
      </div>
    </div>
  </div>
</template>

<script>
import $ from "jquery";

export default {
  data() {
    return {
      forms: [
        "餐飲服務業",
        "食品零售業",
        "住宿及旅館業",
        "布疋、皮革及服飾業",
        "家用器具及用品業",
        "汽機車及其零配件業",
        "美髮及美容美體業",
        "運動健康及其器材用品業",
        "文教育樂用品業",
        "休閒娛樂業",
        "生技產品業",
        "不動產業",
        "其他"
      ],
      forms2: [
        "5人以下(不含5人)",
        "5人~10人(含)",
        "11人~50人",
        "51人~100人",
        "101人以上"
      ],
      email: "",
      username: "",
      password: "",
      password2: "",
      m_name: "",
      c_name: "",
      c_num: "",
      phone: "",
      industry: "不動產業",
      workers: "101人以上"
    };
  },
  methods: {
    back_to_login() {
      this.$router.push("/view/view_home");///view/view_login
    },
    register() {
      var self = this;
      //電話格式是否符合
      //let phone_pattern_error = false;
      /*if (!String(this.phone).match(/^[0-9]{2}-[0-9]{7,8}$/)) {
        //02-66072131
        if (!String(this.phone).match(/^[0-9]{2}-[0-9]{7,8}#[0-9]{2,6}$/)) {
          //02-66072131#1234
          if (!String(this.phone).match(/^09[0-9]{8}$/)) {
            //0987654321
            phone_pattern_error = true;
            self.$swal({
              icon: "error",
              title: "請確認電話格式正確",
              timer: 1500
            });
          }
        }
      } else {
      }*/
      //if (phone_pattern_error === false) {
        if (this.password === this.password2) {
          this.$swal({
            text: `確定要註冊嗎?`,
            icon: "question",
            showCancelButton: true,
            confirmButtonText: "確定",
            cancelButtonText: "取消",
            preConfirm: () => {
              return this.axios
                .post(
                  "/rise_api/public/index.php/view/register",
                  {
                    username:this.username,
                    email: this.email,
                    industry: this.industry,
                    password: this.password,
                    m_name: this.m_name,
                    c_name: this.c_name,
                    c_num: this.c_num,
                    phone: this.phone,
                    workers: this.workers
                  }
                )
                .then(response => {

                 // console.log(response.data);

                  if (response.data[0].status == "1") {

                    
                    this.$swal({
                      title: "驗證信發送中",
                      allowEscapeKey: false,
                      allowOutsideClick: false,
                      timer: 2500,
                      onOpen: () => {
                        this.$swal.showLoading();
                        this.axios
                          .post(
                            "/rise_api/public/index.php/email/send",
                            {
                              email: this.email,
                              token: response.data[0].token
                            }
                          )
                          .then(response => {
                            if (response.status == 200) {
                              this.$swal({
                                icon: "success",
                                title: "請至信箱收取驗證信，並完成註冊",
                                showConfirmButton: false,
                                timer: 2500
                              }).then(() => {
                                this.$router.push({
                                  name: "view_login",
                                  params: { userId: this.email } //傳參數給login頁面 讓註冊完的畫面轉到登入時，會填入帳號
                                });
                              });
                            }
                          });
                      }
                    });
                  }else if (response.data.indexOf("SQLSTATE[23000]") != -1) {
                    //判斷帳號有沒有重複
                    this.$swal({
                      icon: "error",
                      text: "此帳號已申請!"
                    });
                  }
                  //避免有沒判斷到的，拋出錯誤(debug用) 理論上不會出現
                  else {
                    this.$swal({
                      icon: "error",
                      text: "資料有誤!"
                    });
                  }
                });
            }
          });
        } else {
          this.$swal({
            icon: "error",
            text: "密碼與確認密碼不一致!"
          });
        }
      //}
    }
  }
};
</script>

<style>
#all_2 {
  width: 90%;
  margin: auto;
}
#all_3 {
  width: 70%;
  margin: auto;
}
.form-signin input[type="email"] {
  /*  border-bottom-right-radius: 0;
  border-bottom-left-radius: 0;*/
  text-indent: 35px;
  background-size: 30px 35px;
  background-image: url("../assets/email.png");
  background-position: 7px 4px;
  background-repeat: no-repeat;
}
.form-signin input[type="password"] {
  /*  border-top-left-radius: 0;
  border-top-right-radius: 0;*/
  text-indent: 35px;
  background-size: 25px 25px;
  background-image: url("../assets/password.png");
  background-position: 9px 9px;
  background-repeat: no-repeat;
}
.bbbox {
  border: 1px solid #cccccc;
  width: auto;
  height: 200px;
  background-color: #ffffff;
  padding: 5px;
  overflow: auto;
}
.myMOUSE:hover {
  cursor: pointer;
  color: #0056c2;
  text-decoration: underline;
}
</style>



