import Vue from "vue";
import Router from "vue-router";
import axios from "axios";

//前台
import FrontLayout from "@/view/Layout"; //上面的banner
import FrontForm from "@/view/Form"; //
import view_home from "@/view/home"; //首頁
import view_about from "@/view/about"; //關於
import view_register from "@/view/register"; //註冊
import answer from "@/view/answer"; //作答完畢的結果
import answerType1 from "@/view/answer1/index.vue"; //原本的作答結果報表
import answerView1 from "@/view/answer1/view.vue"; // 直取答案頁面 - 1
import answerType2 from "@/view/answer2/index.vue"; //生活服務業競爭力評量報表

import appreciationContent from "@/view/appreciationContent"; //作答完畢的感謝頁
import answer_history from "@/view/answer_history"; //作答完畢的結果(歷史紀錄)
import answer_show from "@/view/answer_show"; // 競爭力評量報表測試頁面
import view_login from "@/view/login"; //登入頁面
import register_check from "@/view/register_check"; //註冊驗證
import password_forget from "@/view/password_forget"; //忘記密碼頁面
import password_reset from "@/view/password_reset"; //密碼重設頁面
import error404 from "@/view/error404"; //404頁面
import user_update from "@/view/user_update"; //編輯基本資料

Vue.use(Router);

let router = new Router({
  routes: [
    {
      path: "/",
      redirect: "/view/view_home"
    },
    //前台
    {
      path: "/view",
      component: FrontLayout,
      name: "View",
      children: [
        {
          path: "form/:id",
          name: "FrontForm",
          component: FrontForm
        },
        {
          path: "form/:id/:preview",
          name: "preview",
          component: FrontForm
        },
        {
          path: "view_home",
          name: "view_home",
          component: view_home
        },
        {
          path: "view_home/:ssoFlag/:id/:password/:formId/:time",
          name: "view_home",
          component: view_home
        },
        {
          path: "view_home/:ssoFlag/:id/:password/:formId/:time/:customer",
          name: "view_home",
          component: view_home
        },
        {
          path: "view_about",
          name: "view_about",
          component: view_about
        },
        {
          path: "view_register",
          name: "view_register",
          component: view_register
        },
        {
          path: "view_login",
          name: "view_login",
          component: view_login
        },
        {
          path: "answer",
          name: "answer",
          component: answer
        },
        {
          path: "answer_1",
          name: "answer_1",
          component: answerType1
        },
        {
          path: "answer_2",
          name: "answer_2",
          component: answerType2
        },
        {
          path: "answer_report_1/:answerEid",
          name: "answer_report_1",
          component: answerView1
        },
        {
          path: "answer_view_1/:answerId",
          name: "answer_view_1",
          component: answerView1
        },
        {
          path: "answer_view_2/:answerId",
          name: "answer_view_2",
          component: answerType2
        },
        {
          path: "answer_report_2/:answerEid",
          name: "answer_report_2",
          component: answerType2
        },
        {
          path: "appreciationContent",
          name: "appreciationContent",
          component: appreciationContent
        },
        {
          path: "answer_show",
          name: "answer_show",
          component: answer_show
        },
        {
          path: "answer_show/:formId/:time",
          name: "answer_show",
          component: answer_show
        },
        {
          path: "answer_history",
          name: "answer_history",
          component: answer_history
        },
        {
          path: "answer_history/:formId/:companyName/:time",
          name: "answer_history",
          component: answer_history
        },
        {
          path: "view_registered_check/:token", //註冊確認
          name: "view_registered_check",
          component: register_check
        },
        {
          path: "view_password_forget", //忘記密碼確認
          name: "view_password_forget",
          component: password_forget
        },
        {
          path: "view_password_reset/:token", //重設密碼確認
          name: "view_password_reset",
          component: password_reset
        },
        {
          path: "user_update",
          name: "user_update",
          component: user_update
        }
      ]
    },
    {
      path: "*",
      component: error404
    }
  ]
});

router.beforeEach((to, from, next) => {
  //前台
  // console.log(localStorage);
  /*if(localStorage.length > 1 && localStorage.username!="guest"){
    if(parseInt(new Date().getTime()/1000)-localStorage.view_time>14400){
      localStorage.clear();
      Vue.swal.fire({
        icon:'error',
        title:'Oops.......',
        text:'登入逾時，請重新登入',
      }).then(() => {
        next('/view/view_login')
      })
    }
  }*/

  if (to.name == "FrontForm") {
    axios
      .post("/rise_api/public/index.php/view/login_check_token", {
        username: localStorage.view_username,
        token: localStorage.view_token
      })
      .then(response => {
        if (response.data != "1") {
          Vue.swal
            .fire({
              icon: "error",
              title: "尚未登入",
              text: "請先進行登入在填寫問卷或使用訪客身分登入",
              showCancelButton: true,
              confirmButtonText: " 立即登入!",
              cancelButtonText: "尚未註冊(立即前往註冊)"
            })
            .then(result => {
              if (result.value) {
                next("/view/view_login");
              } else {
                next("/view/view_register");
              }
            });
        } else {
          localStorage.setItem("view_status", "1");
        }
      });
    next();
  } else {
    next();
  }
});

export default router;



